import { createBatchSender, BatchSender, BatchSenderConfig, stringifyBatch } from '@slardar/sdk-template'

import { getBeaconTransport } from './transports'
import { WebSendEvent } from './types'
import { onPageUnload } from './utils'

// istanbul ignore not working for export { ... } from statements
// https://github.com/istanbuljs/nyc/issues/1209
/* istanbul ignore next */
export { createBatchSender, BatchSender, BatchSenderConfig } from '@slardar/sdk-template'

export type BrowserSender = BatchSender<WebSendEvent>

// createSender has side effects(register onClose behaviour)
// so it must be create lazily
export function createBrowserSender(config: BatchSenderConfig): BrowserSender {
  const sender = createBatchSender<WebSendEvent>(config)
  const beaconTransport = getBeaconTransport()

  const sendOnClose = (data: WebSendEvent) => {
    beaconTransport.post(sender.getEndpoint(), stringifyBatch([data]))
  }

  onPageUnload(() => {
    const data = sender.getBatchData()
    if (data) {
      beaconTransport.post(sender.getEndpoint(), data)
      sender.clear()
    }
    sender.send = sendOnClose
  })

  return sender
}
