/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { isArray, isFunction, isObject, isPlainObject } from './is'

// eslint-disable-next-line @typescript-eslint/ban-types
export function omit<T extends object, K extends (keyof T)[]>(
  object: T,
  ...keys: K
): Pick<T, Exclude<keyof T, K[number]>> {
  if (isObject(object)) {
    const res = {} as Pick<T, Exclude<keyof T, K[number]>>
    objectForIn(object, (k, v) => {
      if (!arrayIncludes(keys, k)) {
        res[k as any] = v
      }
    })
    return res
  }
  return object
}

export function hasKey<T>(object: T, key: string): boolean {
  return Object.prototype.hasOwnProperty.call(object, key)
}

export type ObjectForInCallback<O> = (index: keyof O, item: O[keyof O]) => void
export function objectForIn<O>(object: O, callback: ObjectForInCallback<O>) {
  if (isObject(object)) {
    for (const i in object) {
      if (hasKey<typeof object>(object, i)) {
        callback.call(null, i, object[i])
      }
    }
  }
}

export function filterObjectValueBy<T>(obj: Record<string, T>, p: (v: T) => boolean) {
  if (!isObject(obj)) {
    return obj
  }

  if (!isFunction(p)) {
    return obj
  }

  const res = {} as Record<string, T>
  objectForIn(obj, (key, value) => {
    if (p(value)) {
      res[key] = value
    }
  })

  return res
}

// 把source对象中的内容深度赋给target, 数组覆盖
export function mergeDeepCopyArray<T>(...source: Partial<T>[]): T {
  let result = {} as T
  let k = 0
  while (k < source.length) {
    result = _mergeDeepCopyArray<T>(result, source[k++])
  }

  return result
}

// 把source对象中的内容深度赋给target, 数组合并
export function mergeDeepConcatArray<T>(...source: Partial<T>[]): T {
  let result = {} as T
  let k = 0
  while (k < source.length) {
    result = _mergeDeepMergeArray<T>(result, source[k++])
  }

  return result
}

function _mergeDeepCopyArray<T>(target: Partial<T>, source: Partial<T>): T {
  const result: T = { ...target } as T
  for (const key in source) {
    if (hasKey(source, key) && source[key] !== undefined) {
      if (isObject(source[key]) && isPlainObject(source[key])) {
        result[key] = _mergeDeepCopyArray(isObject(target[key]) ? target[key]! : {}, source[key]!)
      } else if (isArray(source[key])) {
        result[key] = cloneArray(source[key] as unknown as unknown[]) as unknown as T[Extract<keyof T, string>]
      } else {
        result[key] = source[key]!
      }
    }
  }
  return result
}

// 递归赋值
function _mergeDeepMergeArray<T>(target: Partial<T>, source: Partial<T>): T {
  const result: T = { ...target } as T
  for (const key in source) {
    if (hasKey(source, key) && source[key] !== undefined) {
      if (isObject(source[key]) && isPlainObject(source[key])) {
        result[key] = _mergeDeepMergeArray(isObject(target[key]) ? target[key]! : {}, source[key]!)
      } else if (isArray(source[key]) && isArray(target[key])) {
        result[key] = _mergeDeepArray(target[key], source[key]) as unknown as T[Extract<keyof T, string>]
      } else {
        result[key] = source[key]!
      }
    }
  }
  return result
}

function _mergeDeepArray<T, U>(target: T[] | any, source: U[] | any): (T | U)[] {
  const _target: T[] = isArray(target) ? (target as T[]) : []
  const _source: U[] = isArray(source) ? (source as U[]) : []
  return Array.prototype.concat.call(_target, _source).map((v) => {
    if (v instanceof RegExp) {
      return v
    } else if (isObject(v) && isPlainObject(v)) {
      return _mergeDeepMergeArray({}, v)
    } else if (isArray(v)) {
      return _mergeDeepArray([], v)
    } else {
      return v
    }
  })
}

export function cloneArray<T extends any>(array: T[]) {
  return array.slice()
}

// 检查数组中是否有元素
export function arrayIncludes<T>(array: T[], value: T) {
  if (!isArray(array)) {
    return false
  }

  if (array.length === 0) {
    return false
  }

  let k = 0
  while (k < array.length) {
    if (array[k] === value) {
      return true
    }
    k++
  }

  return false
}

export const arrayRemove = <T>(arr: T[], e: T) => {
  if (!isArray(arr)) {
    return arr
  }
  const i = arr.indexOf(e)
  if (i >= 0) {
    const arr_ = arr.slice()
    arr_.splice(i, 1)
    return arr_
  }
  return arr
}

export interface Visitor {
  (target: any, property: string): any
}

/**
 * 按路径访问对象属性
 * @param target 待访问对象
 * @param property 访问属性路径
 * @param { (target: any, property: string): any } visitor 访问器
 */
export const safeVisit = (target: any, path: string, visitor: Visitor) => {
  const paths = path.split('.')
  let [method, ...rest] = paths
  while (target && rest.length > 0) {
    target = target[method]
    ;[method, ...rest] = rest
  }
  if (!target) {
    return undefined
  }
  return visitor(target, method)
}

export const safeSet = (target: any, path: string, value: any) => {
  safeVisit(target, path, (obj: any, property: string) => {
    try {
      obj[property] = value
    } catch (e) {
      // ignore
    }
  })
}

/**
 *  按路径调用函数
 * @param target 待调用对象，如 `client`
 * @param methods 待调用方法路径，可能是一级路径 `client.start`, 或者是多级命令 `client.context.set`
 * @param args 调用参数
 */
export const safeCall = (target: any, method: string, args: any[]) => {
  return safeVisit(target, method, (obj: any, property: string) => {
    if (obj && property in obj && isFunction(obj[property])) {
      try {
        return obj[property].apply(obj, args)
      } catch (err) {
        // ignore
        return undefined
      }
    }
  })
}

/**
 *  按路径调用函数或获取值
 * @param target 待调用对象，如 `client`
 * @param methods 待调用方法路径或属性路径
 * @param args 调用参数
 */
export const safeCallOrGet = (target: any, method: string, args?: any[]) => {
  return safeVisit(target, method, (obj: any, property: string) => {
    if (obj && property in obj) {
      // call
      if ((args && args.length) || isFunction(obj[property])) {
        try {
          return obj[property].apply(obj, args)
        } catch (err) {
          // ignore
          return undefined
        }
      } else {
        // get
        return obj[property]
      }
    }
  })
}

export const applyRecord = <K extends keyof any, T>() => {
  const record: Record<K, T> = {} as any
  const set = (key: K, val: T) => (record[key] = val)
  const del = (key: K) => delete record[key]

  return [record, set, del] as const
}

export const pick = <T extends Record<string, unknown>, K extends (keyof T)[]>(obj: T, keys: K): Pick<T, K[number]> => {
  if (!obj || !isObject(obj)) return obj

  return keys.reduce((prev, cur) => {
    prev[cur] = obj[cur]
    return prev
  }, {} as Pick<T, K[number]>)
}
