/* eslint-disable @typescript-eslint/prefer-for-of */
import { isString } from '@slardar/sdk-template'

import { getDefaultDocument } from './defaults'

export const getCookieId = (cookieIdName: string) => {
  const document = getDefaultDocument()
  try {
    if (document?.cookie) {
      return getValueFromCookieName(document.cookie, cookieIdName)
    }
  } catch (_e) {
    // do nothing
  }
  return ''
}

export const setCookie = (key: string, value: string) => {
  const document = getDefaultDocument()
  if (document && key) {
    try {
      document.cookie = `${key}=${value};max-age=${90 * 24 * 60 * 60}`
    } catch (_e) {
      // do nothing
    }
  }
}

export function getValueFromCookieName(cookie: string, name: string) {
  if (!cookie || !name) {
    return ''
  }
  return parseCookie(cookie)[name] || ''
}

function parseCookie(cookie: string): Record<string, string> {
  const list = cookie.split(';')
  const cookieObj = {}

  for (let i = 0; i < list.length; i++) {
    const pair = list[i].split('=')
    const cookieKey = isString(pair[0]) && pair[0].trim()
    if (cookieKey && isString(pair[1])) {
      cookieObj[cookieKey] = pair[1].trim()
    }
  }

  return cookieObj
}
