import { SafeObservable } from '@slardar/sdk-template'

import { PVNext, HiddenNext, UnloadNext } from '../../collector'
import { observePerfWithBuffer } from '../../collector/pref'
import { getDefaultPerformanceObserver } from '../../utils'
import { MetricEnum } from '../performance/type'
import { initMetric } from '../performance/util'

import { PerfMetricGetterWithHiddenContext } from './type'
import { applyReportMetric } from './util'

interface LayoutShift extends PerformanceEntry {
  value: number
  hadRecentInput: boolean
}

export const LS_ENTRY_NAME = 'layout-shift'
export const CLS_METRIC_NAME = MetricEnum.cls

export const applyHandleEntries = () => {
  let sessionValue = 0
  let sessionTimes: number[] = []
  const resetSessionValue = () => {
    sessionValue = 0
  }
  const handleEntriesWithMetric = (callback: (currentSessionValue: number) => void, entry: LayoutShift) => {
    // Only count layout shifts without recent user input.
    if (!entry.hadRecentInput) {
      const firstSessionTime = sessionTimes[0]
      const lastSessionTime = sessionTimes[sessionTimes.length - 1]

      if (sessionValue && entry.startTime - lastSessionTime < 1000 && entry.startTime - firstSessionTime < 5000) {
        sessionValue += entry.value
        sessionTimes.push(entry.startTime)
      } else {
        sessionValue = entry.value
        sessionTimes = [entry.startTime]
      }
      callback(sessionValue)
    }
  }
  return [resetSessionValue, handleEntriesWithMetric] as const
}

const getCLS: PerfMetricGetterWithHiddenContext = (
  report,
  tearDownGroup,
  [hiddenObserver, unloadObserver, pvObserver]: [
    SafeObservable<HiddenNext>,
    SafeObservable<UnloadNext>,
    SafeObservable<PVNext>,
  ],
) => {
  const PerformanceObserver = getDefaultPerformanceObserver()

  let metric = initMetric(CLS_METRIC_NAME, 0)

  const reportMetric = applyReportMetric(report)

  if (!PerformanceObserver) {
    metric.isSupport = false
    reportMetric(metric)
    return
  }

  const [resetSessionValue, handleEntriesWithMetric] = applyHandleEntries()

  const handleEntries = handleEntriesWithMetric.bind(null, (sessionValue) => {
    sessionValue > metric.value && (metric.value = sessionValue)
  })

  tearDownGroup.push(observePerfWithBuffer(PerformanceObserver, handleEntries, LS_ENTRY_NAME))

  tearDownGroup.push(hiddenObserver[0](resetSessionValue))

  tearDownGroup.push(
    pvObserver[0]((overrides) => {
      reportMetric(metric, overrides)
      resetSessionValue()
      metric = initMetric(CLS_METRIC_NAME, 0)
    }),
  )

  tearDownGroup.push(
    unloadObserver[0](() => {
      reportMetric(metric)
    }),
  )
}

export const CLS: [MetricEnum, PerfMetricGetterWithHiddenContext] = [CLS_METRIC_NAME, getCLS]
