import { Observer } from '@slardar/sdk-template'

import { applyRequestIdleCallback, invokeCallbackOnce } from '../utils'
import { getDefaultBrowser, getDefaultDocument } from '../utils/defaults'

export const HIDDEN_SUBJECT_NAME = 'hidden_0'

// eslint-disable-next-line @typescript-eslint/no-invalid-void-type
export type HiddenNext = (args: void) => void

export const observeHidden: Observer<HiddenNext> = (next, tearDown) => {
  const window = getDefaultBrowser()
  const document = getDefaultDocument()
  if (!window || !document) return

  const onVisibilityChange = () => {
    if (document.visibilityState === 'hidden') {
      next()
    }
  }
  addEventListener('visibilitychange', onVisibilityChange, true)

  const requestIdleCallback = applyRequestIdleCallback(window)

  tearDown(
    () => {
      removeEventListener('visibilitychange', onVisibilityChange, true)
    },
    (subscriber) => {
      if (document.visibilityState === 'hidden') {
        requestIdleCallback(() => {
          subscriber()
        })
      }
    },
  )
}

export const hiddenSubject: [string, Observer<HiddenNext>] = [HIDDEN_SUBJECT_NAME, observeHidden]

export const LOAD_SUBJECT_NAME = 'load_0'

// eslint-disable-next-line @typescript-eslint/no-invalid-void-type
export type LoadNext = (args: void) => void

export const observeLoad: Observer<LoadNext> = (next, tearDown) => {
  const window = getDefaultBrowser()
  const document = getDefaultDocument()
  if (!window || !document) return

  const onLoad = () => {
    setTimeout(() => {
      next()
    }, 0)
  }
  window.addEventListener('load', onLoad, false)

  const requestIdleCallback = applyRequestIdleCallback(window)
  tearDown(
    () => {
      window.removeEventListener('load', onLoad, false)
    },
    (subscriber) => {
      if (document.readyState === 'complete') {
        requestIdleCallback(() => {
          subscriber()
        })
      }
    },
  )
}

export const loadSubject: [string, Observer<LoadNext>] = [LOAD_SUBJECT_NAME, observeLoad]

export const UNLOAD_SUBJECT_NAME = 'unload_0'

// eslint-disable-next-line @typescript-eslint/no-invalid-void-type
export type UnloadNext = (args: void) => void

export const observeUnload: Observer<UnloadNext> = (next, tearDown) => {
  const window = getDefaultBrowser()
  if (!window) return

  // eslint-disable-next-line @typescript-eslint/no-invalid-void-type
  const [invokeCbOnce] = invokeCallbackOnce<void>(next)
  const unloadEventList = ['unload', 'beforeunload', 'pagehide']

  const handler = () => {
    invokeCbOnce()
  }

  unloadEventList.forEach((ev) => {
    window.addEventListener(ev, handler)
  })
  tearDown(() => {
    unloadEventList.forEach((ev) => {
      window.removeEventListener(ev, handler)
    })
  })
}

export const unloadSubject: [string, Observer<UnloadNext>] = [UNLOAD_SUBJECT_NAME, observeUnload]
