/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { WebReportEvent } from '../../types'
import { parseUrl } from '../../utils'

import { PageviewSourceType } from './type'

export const getDefaultExtractor = (routeMode: 'hash' | 'history' | 'manual') => (url: string) => {
  if (routeMode === 'hash') {
    return parseUrl(url).hash?.replace(/^#/, '') || '/'
  } else {
    return parseUrl(url).path
  }
}

export const applyOnPidChange = (
  cb: (source: PageviewSourceType, pid: string) => void,
  initPid: string,
  firstOriginPid: string,
  onPidUpdate?: (pid: string) => void,
): [(source: PageviewSourceType, newPid: string, customPid?: string) => void, () => void] => {
  // store originalPid from url
  let originalPid = firstOriginPid
  // store finalPid send to server
  let finalPid = initPid
  // set init pid in config
  onPidUpdate && onPidUpdate(initPid)
  const onPidChange = (source: PageviewSourceType, newPid: string, customPid?: string) => {
    // newPid is extractPid from url, should send pageview if changed, no matter customPid changed
    if (source !== PageviewSourceType.user_set && newPid !== originalPid) {
      originalPid = newPid
      finalPid = customPid ?? originalPid
      onPidUpdate && onPidUpdate(finalPid) // update pid in common
      cb(source, finalPid)
      // newPid is by user set, should send pageview if not equal to finalPid
    } else if (source === PageviewSourceType.user_set && newPid !== finalPid) {
      finalPid = newPid
      onPidUpdate && onPidUpdate(finalPid) // update pid in common
      cb(source, finalPid)
    }
  }
  const sendInit = () => {
    initPid && cb(PageviewSourceType.init, initPid)
  }
  return [onPidChange, sendInit]
}

export const applyOnUrlChange = (cb: (source: PageviewSourceType, url: string) => void, initUrl: string) => {
  let url = initUrl
  const onUrlChange = (source: PageviewSourceType, newUrl: string) => {
    if (newUrl !== url) {
      url = newUrl
      cb(source, url)
    }
  }
  return [onUrlChange]
}

export const wrapPageview = (source: PageviewSourceType, pid: string): WebReportEvent => ({
  ev_type: 'pageview',
  payload: {
    pid,
    source,
  },
})

export const applyReportPageview = (report: (ev: WebReportEvent) => void) => {
  return (source: PageviewSourceType, pid: string) => {
    report(wrapPageview(source, pid))
  }
}

export const isManualMode = (routeMode: string) => routeMode === 'manual'
