import { SafeObservable } from '@slardar/sdk-template'

import { HiddenNext, UnloadNext, observePerfWithBuffer, PVNext } from '../../collector'
import { getDefaultPerformanceObserver } from '../../utils'
import { MetricEnum } from '../performance/type'
import { initMetric } from '../performance/util'

import { PerfMetricGetterWithHiddenContext } from './type'
import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

export const LCP_ENTRY_NAME = 'largest-contentful-paint'
export const LCP_METRIC_NAME = MetricEnum.lcp

const LCPListenerEventTypes = ['keydown', 'click']

const getLCP: PerfMetricGetterWithHiddenContext = (
  report,
  tearDownGroup,
  [hiddenObserver, unloadObserver]: [SafeObservable<HiddenNext>, SafeObservable<UnloadNext>, SafeObservable<PVNext>],
) => {
  const PerformanceObserver = getDefaultPerformanceObserver()

  const metric = initMetric(LCP_METRIC_NAME, 0)
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)

  if (!PerformanceObserver) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  tearDownGroup.push(
    observePerfWithBuffer(
      PerformanceObserver,
      ({ startTime }) => {
        metric.value = startTime
      },
      LCP_ENTRY_NAME,
    ),
  )

  const inputEventHandler = () => {
    sendOnceAndTearDown(metric)
  }

  LCPListenerEventTypes.forEach((type) => {
    window.addEventListener(type, inputEventHandler, true)
    tearDownGroup.push(() => {
      window.removeEventListener(type, inputEventHandler, true)
    })
  })

  tearDownGroup.push(
    hiddenObserver[0](() => {
      metric.isSupport = false
      sendOnceAndTearDown(metric)
    }),
  )

  tearDownGroup.push(
    unloadObserver[0](() => {
      metric.isBounced = true
      sendOnceAndTearDown(metric)
    }),
  )
}

export const LCP: [MetricEnum, PerfMetricGetterWithHiddenContext] = [LCP_METRIC_NAME, getLCP]
