import { getRegexp } from '@slardar/sdk-template'

import { applyPerformance } from '../../utils'

const headerKeyRe = new RegExp('(cookie|auth|jwt|token|key|ticket|secret|credential|session|password)', 'i')
const headerValueRe = new RegExp('(bearer|session)', 'i')

export const getGetLatestEntryByName = (performance?: Performance) => {
  const [, , , , getEntriesByName] = applyPerformance(performance)
  return (name: string) => {
    return getEntriesByName(name).pop()
  }
}

export const checkIsIgnored = (ignoreUrls: (string | RegExp)[], url: string): boolean => {
  const ignoreRgx = getRegexp(ignoreUrls || [])
  return !!ignoreRgx && (ignoreRgx as RegExp).test(url)
}

export const isSensitiveHeader = (key: string, value: string) => {
  if (!key || !value) return false
  return headerKeyRe.test(key) || headerValueRe.test(value)
}

export const applySendOnce = <T>(cb: (metric: T) => void) => {
  let hasInvoked = false
  return (metric: T) => {
    if (hasInvoked) return
    hasInvoked = true
    cb(metric)
  }
}
