import { noop } from './misc'

export const BUFFER_TIME = 300000 // 5分钟

export const applyBufferTimer = (checkIfNeedCancel: () => boolean, cb: () => void, bufferTime: number) => {
  let tid = 0
  if (bufferTime === -1) return noop
  return () => {
    if (checkIfNeedCancel()) {
      tid && clearTimeout(tid)
      tid = 0
      return
    }
    tid === 0 && (tid = (setTimeout as Window['setTimeout'])(cb, bufferTime))
  }
}
