/**
 * from sentry https://github.com/getsentry/sentry-javascript/blob/5.30.0/packages/utils/src/browser.ts
 */

import { isString } from '@slardar/sdk-template'

import { Breadcrumb } from '../types'

export const UNKNOWN_PATH = '<unknown>'

/**
 * Given a child DOM element, returns a query-selector statement describing that
 * and its ancestors
 * e.g. [HTMLElement] => body > div > input#foo.btn[name=baz]
 * @returns generated DOM path
 */
export function htmlTreeAsString(elem: unknown): string {
  type SimpleNode = {
    parentNode: SimpleNode
  } | null

  // try/catch both:
  // - accessing event.target (see getsentry/raven-js#838, #768)
  // - `htmlTreeAsString` because it's complex, and just accessing the DOM incorrectly
  // - can throw an exception in some circumstances.
  try {
    let currentElem = elem as SimpleNode
    const MAX_TRAVERSE_HEIGHT = 5
    const MAX_OUTPUT_LEN = 80
    const out: string[] = []
    let height = 0
    let len = 0
    const separator = ' > '
    const sepLength = separator.length
    let nextStr

    while (currentElem && height++ < MAX_TRAVERSE_HEIGHT) {
      nextStr = _htmlElementAsString(currentElem)
      // bail out if
      // - nextStr is the 'html' element
      // - the length of the string that would be created exceeds MAX_OUTPUT_LEN
      //   (ignore this limit if we are on the first iteration)
      if (nextStr === 'html' || (height > 1 && len + out.length * sepLength + nextStr.length >= MAX_OUTPUT_LEN)) {
        break
      }

      out.push(nextStr)

      len += nextStr.length
      currentElem = currentElem.parentNode
    }

    return out.reverse().join(separator)
  } catch (_oO) {
    return UNKNOWN_PATH
  }
}

/**
 * Returns a simple, query-selector representation of a DOM element
 * e.g. [HTMLElement] => input#foo.btn[name=baz]
 * @returns generated DOM path
 */
function _htmlElementAsString(el: unknown): string {
  const elem = el as {
    getAttribute: (key: string) => string // tslint:disable-line:completed-docs
    tagName?: string
    id?: string
    className?: string
  }

  const out: string[] = []
  let classes
  let key
  let attr
  let i

  if (!elem || !elem.tagName) {
    return ''
  }

  out.push(elem.tagName.toLowerCase())
  if (elem.id) {
    out.push(`#${elem.id}`)
  }

  const className = elem.className
  if (className && isString(className)) {
    classes = className.split(/\s+/)
    for (i = 0; i < classes.length; i++) {
      out.push(`.${classes[i]}`)
    }
  }
  const attrlist = ['type', 'name', 'title', 'alt']
  for (i = 0; i < attrlist.length; i++) {
    key = attrlist[i]
    attr = elem.getAttribute(key)
    if (attr) {
      out.push(`[${key}="${attr}"]`)
    }
  }
  return out.join('')
}

type InstrumentHandlerType = 'dom'

export const applyDomAndKeyPress = (debounceDuration: number) => {
  let keypressTimeout: number | undefined

  /**
   * Wraps addEventListener to capture UI breadcrumbs
   * @param name the event name (e.g. "click")
   * @param handler function that will be triggered
   * @param debounce decides whether it should wait till another event loop
   * @returns wrapped breadcrumb events handler
   * @hidden
   */
  const domEventHandler = (name: string, handler: (params: { event: Event; name: string }) => void) => {
    let lastCapturedEvent: Event | undefined

    return (event: Event) => {
      // reset keypress timeout; e.g. triggering a 'click' after
      // a 'keypress' will reset the keypress debounce so that a new
      // set of keypresses can be recorded
      keypressTimeout = undefined
      // It's possible this handler might trigger multiple times for the same
      // event (e.g. event propagation through node ancestors). Ignore if we've
      // already captured the event.
      if (!event || lastCapturedEvent === event) {
        return
      }

      lastCapturedEvent = event
      handler({ event, name })
    }
  }

  /**
   * Wraps addEventListener to capture keypress UI events
   * @param handler function that will be triggered
   * @returns wrapped keypress events handler
   * @hidden
   */
  const keypressEventHandler = (handler: (params: { event: Event; name: string }) => void) => {
    // TODO: if somehow user switches keypress target before
    //       debounce timeout is triggered, we will only capture
    //       a single breadcrumb from the FIRST target (acceptable?)
    return (event: Event) => {
      let target

      try {
        target = event.target
      } catch (e) {
        // just accessing event properties can throw an exception in some rare circumstances
        // see: https://github.com/getsentry/raven-js/issues/838
        return
      }

      const tagName = target && (target as HTMLElement).tagName

      // only consider keypress events on actual input elements
      // this will disregard keypresses targeting body (e.g. tabbing
      // through elements, hotkeys, etc)
      if (!tagName || (tagName !== 'INPUT' && tagName !== 'TEXTAREA' && !(target as HTMLElement).isContentEditable)) {
        return
      }

      // record first keypress in a series, but ignore subsequent
      // keypresses until debounce clears
      !keypressTimeout && domEventHandler('input', handler)(event)
      clearTimeout(keypressTimeout)

      keypressTimeout = window.setTimeout(() => {
        keypressTimeout = undefined
      }, debounceDuration)
    }
  }

  return [domEventHandler, keypressEventHandler] as const
}

export const triggerHandlers =
  (domBreadcrumb: (handlerData: { event: Event; name: string }) => void, type: InstrumentHandlerType) =>
  (data: { event: Event; name: string }) => {
    if (!type) {
      return
    }

    try {
      domBreadcrumb(data)
    } catch (e) {
      // ignore
    }
  }

/**
 * Creates breadcrumbs from DOM API calls
 */
export const domBreadcrumb =
  (addBreadcrumb: (bd: Breadcrumb) => void) => (handlerData: { event: Event; name: string }) => {
    let target

    // Accessing event.target can throw (see getsentry/raven-js#838, #768)
    try {
      target = handlerData.event.target
        ? htmlTreeAsString(handlerData.event.target as Node)
        : htmlTreeAsString(handlerData.event as unknown as Node)
    } catch (e) {
      target = '<unknown>'
    }

    if (target.length === 0) {
      return
    }

    addBreadcrumb({
      type: 'dom',
      category: `ui.${handlerData.name}`,
      message: target,
    } as Breadcrumb)
  }
