export const createLogger = (prefix: string, prefixLen = 20) => {
  let count = 0
  return (...args: any[]) => {
    // eslint-disable-next-line no-console
    console.log(prefix.padStart(prefixLen, ' '), Date.now(), `${count++}`.padStart(8, ' '), ...args)
  }
}

let count = 0
export const log = (...args: any[]) => {
  // eslint-disable-next-line no-console
  console.log('[SDK]', Date.now(), `${count++}`.padStart(8, ' '), ...args)
}

let errCount = 0
export const error = (...args: any[]) => {
  // eslint-disable-next-line no-console
  console.error('[SDK]', Date.now(), `${errCount++}`.padStart(8, ' '), ...args)
}

let warnCount = 0
export const warn = (...args: any[]) => {
  // eslint-disable-next-line no-console
  console.warn('[SDK]', Date.now(), `${warnCount++}`.padStart(8, ' '), ...args)
}
