import { initSubjectInGlobal, WithSubject, id, createSafeObserver, WithContext } from '@slardar/sdk-template'

import { applyXHR, XHR_SUBJECT_NAME } from '../../collector/http'
import { resourceSubject } from '../../collector/pref'
import { setTraceContext, setVolTraceContext } from '../../monitors/http/trace'
import { AjaxMonitorConfig, WrapXhr } from '../../monitors/http/type'
import { httpGetterWithXhrObserver } from '../../monitors/http/xhr'
import { HttpReport, ReportToReportEvent, WebClient, WebVolClient } from '../../types'
import { getLocationUrl } from '../../utils'

import { getPluginConfig } from './utils'

export const AJAX_MONITOR_PLUGIN_NAME = 'ajax'

export type WithWrapXhr<C extends WithSubject<WebClient>> = C & {
  wrapXhr?: WrapXhr
}

const defaultConfig: AjaxMonitorConfig = {
  autoWrap: true,
  hookCbAtReq: id,
  ignoreUrls: [],
  collectBodyOnError: false,
}

export interface AjaxMonitorPluginConfig {
  [AJAX_MONITOR_PLUGIN_NAME]?: Partial<AjaxMonitorConfig> | boolean
}

export const getCbHook = (client: WithContext<WebClient>) => (cb?: (ev: ReportToReportEvent<HttpReport>) => void) => {
  if (!cb) return cb
  const clientConfig = client.config()!
  const common = {
    url: getLocationUrl(),
    pid: clientConfig.pid,
    view_id: clientConfig.viewId,
    context: client.context?.toString(),
  }
  return (ev: ReportToReportEvent<HttpReport>) => {
    cb({
      ...ev,
      overrides: {
        ...common,
        timestamp: ev.payload.request.timestamp,
      },
    })
  }
}

export function AjaxMonitorPlugin<C extends WithContext<WithSubject<WebClient>>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, AJAX_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }
    const tearDownGroup: (() => void)[] = []

    const xhrConfig = { ...config, hookCbAtReq: getCbHook(client), setTraceHeader: setTraceContext(config.trace) }
    const getResourceSubject = () => initSubjectInGlobal(client, resourceSubject)

    xhrConfig.autoWrap &&
      httpGetterWithXhrObserver(
        client.report.bind(client),
        tearDownGroup,
        [
          initSubjectInGlobal(client, [XHR_SUBJECT_NAME, applyXHR(XMLHttpRequest && XMLHttpRequest.prototype)]),
          getResourceSubject,
        ],
        xhrConfig,
      )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })

    client.provide('wrapXhr', (Xhr: typeof XMLHttpRequest) => {
      function Ctor() {
        const xhr = new Xhr()
        httpGetterWithXhrObserver(
          client.report.bind(client),
          tearDownGroup,
          [createSafeObserver(applyXHR(xhr)), getResourceSubject],
          xhrConfig,
        )
        return xhr
      }
      Ctor.prototype = new Xhr()
      ;['DONE', 'HEADERS_RECIEVED', 'LOADING', 'OPENED', 'UNSENT'].forEach((key) => {
        Ctor[key] = Xhr[key]
      })
      return Ctor as any as typeof XMLHttpRequest
    })
  })
}

export function AjaxMonitorVolPlugin<C extends WithSubject<WebVolClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, AJAX_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    const xhrConfig = {
      ...config,
      hookCbAtReq: getCbHook(client),
      setTraceHeader: setVolTraceContext(config.trace, `app_id=${client.config()?.aid},origin=web`),
    }
    const getResourceSubject = () => initSubjectInGlobal(client, resourceSubject)

    xhrConfig.autoWrap &&
      httpGetterWithXhrObserver(
        client.report.bind(client),
        tearDownGroup,
        [
          initSubjectInGlobal(client, [XHR_SUBJECT_NAME, applyXHR(XMLHttpRequest && XMLHttpRequest.prototype)]),
          getResourceSubject,
        ],
        xhrConfig,
      )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
  })
}
