import { SafeObservable, getRegexp } from '@slardar/sdk-template'

import { ErrorNext, PromiseRejectionNext } from '../../collector/error'
import { ReportToReportEvent, JsErrorReport, JsErrorPayload } from '../../types'
import { ReactInfo } from '../../types/thrift/browser/collector_v2/js_error'

import { hookGlobalAsync } from './capture-global'
import { dedupe } from './dedupe'
import { normalizeError, normalizeException, normalizeUnknownError } from './util'

export type ReportJsError = (ex: any, extra?: Record<string, string>, react?: ReactInfo) => void

export interface JsErrorConfig {
  ignoreErrors: (string | RegExp)[]
  onerror: boolean
  onunhandledrejection: boolean
  captureGlobalAsync?: boolean
  dedupe?: boolean
}

export const JS_ERROR_EV_TYPE = 'js_error'

type JSErrorGetterWithContext = (
  report: (ev: ReportToReportEvent<JsErrorReport>) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<ErrorNext>, SafeObservable<PromiseRejectionNext>],
  config: JsErrorConfig,
) => ReportJsError

export const jsErrorGetterWithContext: JSErrorGetterWithContext = (
  report,
  tearDownGroup,
  [errorObserver, promiseRejectionObserver],
  config,
) => {
  const { ignoreErrors, onerror, onunhandledrejection, dedupe: dedupeFlag, captureGlobalAsync } = config
  const ignoreRegExp = getRegexp(ignoreErrors)
  const dedupeFn = dedupe()

  const reportJsError = ({ error, extra, react, source }: Partial<JsErrorPayload>) => {
    const err = dedupeFlag ? dedupeFn(error) : error
    if (!err || (ignoreRegExp && ignoreRegExp.test(err.message))) {
      return
    }
    report({
      ev_type: JS_ERROR_EV_TYPE,
      payload: {
        error: err,
        breadcrumbs: [],
        extra,
        react,
        source,
      },
    })
  }

  onerror &&
    tearDownGroup.push(
      errorObserver[0]((ev: ErrorEvent) => reportJsError({ error: normalizeError(ev), source: { type: 'onerror' } })),
    )

  onunhandledrejection &&
    tearDownGroup.push(
      promiseRejectionObserver[0]((ev: PromiseRejectionEvent) =>
        reportJsError({ error: normalizeException(ev), source: { type: 'onunhandledrejection' } }),
      ),
    )

  captureGlobalAsync && tearDownGroup.push(...hookGlobalAsync(reportJsError))

  return (err, extra, react) =>
    reportJsError({ error: normalizeUnknownError(err), extra, react, source: { type: 'manual' } })
}
