import { WithSubject, initSubjectInGlobal } from '@slardar/sdk-template'

import { loadSubject, unloadSubject, hiddenSubject } from '../../collector/page'
import { getFMP } from '../../monitors/performance-new/fmp'
import { getPaint, FP_ENTRY_NAME } from '../../monitors/performance-new/paint'
import { FMPMonitorConfig } from '../../monitors/performance/fmp-monitor/type'
import { WebClient } from '../../types'

import { getPluginConfig, reportOnInitCommonParams } from './utils'

export const FMP_MONITOR_PLUGIN_NAME = 'fmp'

const defaultConfig: FMPMonitorConfig = {
  renderType: 'CSR',
}

export interface FMPMonitorPluginConfig {
  [FMP_MONITOR_PLUGIN_NAME]?: Partial<FMPMonitorConfig> | boolean
}

export function FMPMonitorPlugin<C extends WithSubject<WebClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, FMP_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []
    const loadObserver = initSubjectInGlobal(client, loadSubject)

    if (config.renderType === 'SSR') {
      const hiddenObserver = initSubjectInGlobal(client, hiddenSubject)
      const unloadObserver = initSubjectInGlobal(client, unloadSubject)
      // @ts-expect-error
      getPaint(FP_ENTRY_NAME, FMP_MONITOR_PLUGIN_NAME, reportOnInitCommonParams(client), tearDownGroup, [
        hiddenObserver,
        unloadObserver,
      ])
    } else {
      getFMP(reportOnInitCommonParams(client), tearDownGroup, [loadObserver])
    }

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
  })
}
