/* eslint-disable @typescript-eslint/prefer-optional-chain */
/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { isFunction, mergeDeepConcatArray, noop } from '@slardar/sdk-template'

import { CallBack, MabyTuple, MonitorConstructor } from '../types'

import { reportSelfError } from './registry'

export const applyMutationObserver = (
  MutationObserver: typeof window.MutationObserver | undefined | null | false,
  callback: MutationCallback,
) => {
  // eslint-disable-next-line compat/compat
  const observer = MutationObserver && new MutationObserver(callback)

  const observe = (target?: Node | null | false, options?: MutationObserverInit) => {
    observer && target && observer.observe(target, options)
  }

  const disconnect = () => observer && observer.disconnect()

  return [observe, disconnect] as const
}

export const applyAnimationFrame = (
  document?: Document,
  originRAF?: Window['requestAnimationFrame'],
  originCAF?: Window['cancelAnimationFrame'],
  force?: boolean,
) => {
  const requestAnimationFrame =
    // eslint-disable-next-line compat/compat
    !isFunction(originRAF) || (force && document && document.hidden)
      ? (cb: FrameRequestCallback) => {
          cb(0)
          return 0
        }
      : originRAF
  const cancelAnimationFrame = isFunction(originCAF) ? originCAF : noop

  /**
   * 以 animationFrame 调用函数，如果一帧内多次调用，则会取消前面的调用
   */
  let af: number
  const scheduleAnimationFrame = (cb: FrameRequestCallback) => {
    af && cancelAnimationFrame(af)
    af = requestAnimationFrame(cb)
  }

  return [scheduleAnimationFrame, requestAnimationFrame, cancelAnimationFrame] as const
}

export const applyPerformance = (performance: Performance | undefined | null | false) => {
  // eslint-disable-next-line compat/compat
  const timing: PerformanceTiming | undefined = (performance && performance.timing) || undefined

  const now = () => {
    if (performance && performance.now) return performance.now()
    const time = Date.now ? Date.now() : +new Date()
    const start = (timing && timing.navigationStart) || 0
    return time - start
  }

  const getEntriesByType = (type: string) => {
    const { getEntriesByType } = performance || {}
    return (isFunction(getEntriesByType) && getEntriesByType.call(performance, type)) || []
  }

  const getEntriesByName = (name: string) => {
    const { getEntriesByName } = performance || {}
    return (isFunction(getEntriesByName) && getEntriesByName.call(performance, name)) || []
  }

  const clearResourceTiming = () => {
    const { clearResourceTimings } = performance || {}
    isFunction(clearResourceTimings) && clearResourceTimings.call(performance)
  }

  return [timing, now, getEntriesByType, clearResourceTiming, getEntriesByName] as const
}

export const applyPerformanceObserver = <C extends PerformanceEntry = PerformanceEntry>(
  PerformanceObserver: typeof window.PerformanceObserver | undefined | null | false,
  callback: (entry: C, index: number, array: C[], ob: PerformanceObserver) => void,
  once?: boolean,
  onFail?: () => void,
) => {
  const observer =
    PerformanceObserver &&
    // eslint-disable-next-line compat/compat
    new PerformanceObserver((list, ob) => {
      if (list.getEntries) {
        list.getEntries().forEach((val, i, arr) => callback(val as C, i, arr as C[], ob))
      } else {
        onFail && onFail()
      }
      once && ob.disconnect()
    })

  const observe = (...types: string[]) => {
    if (!PerformanceObserver || !observer) return onFail && onFail()
    try {
      types.forEach((type) => {
        if (PerformanceObserver.supportedEntryTypes.indexOf(type) > -1) {
          observer.observe({ type, buffered: false })
        }
      })
    } catch {
      try {
        observer.observe({ entryTypes: types })
      } catch {
        return onFail && onFail()
      }
    }
  }

  const disconnect = () => observer && observer.disconnect()

  return [observe, disconnect] as const
}

// eslint-disable-next-line @typescript-eslint/no-invalid-void-type
export const applyMonitor = <O, S, D extends any[], R extends any[] | undefined | void>(
  monitorCtor: MonitorConstructor<O, S, D, R>,
  props: O = {} as O,
  cb?: CallBack<S>,
  deps: MabyTuple<D> = [] as any,
): R => {
  try {
    const monitor = monitorCtor(...deps)
    return (monitor && monitor(props, cb)) || ([] as any)
  } catch (e) {
    reportSelfError(e)
    return [] as any
  }
}

// eslint-disable-next-line @typescript-eslint/ban-types
export const applyUpdate = <T extends {}>(init: T) => {
  let state = init
  const result: T = {} as any
  const defineGetter = () =>
    Object.keys(state).forEach(
      (key) =>
        !(key in result) &&
        Object.defineProperty(result, key, {
          get: function () {
            return state[key]
          },
        }),
    )

  const update = (newObj: Partial<T>) => {
    state = mergeDeepConcatArray(state, newObj)
    defineGetter()
  }

  defineGetter()
  return [result, update] as const
}

export const applyRequestIdleCallback = (win: Window | undefined) => {
  return (
    win!.requestIdleCallback ||
    function (cb: () => void) {
      return win!.setTimeout(cb, 1)
    }
  )
}
