import { noop } from '@slardar/sdk-template'

import { getDefaultXMLHttpRequest } from '../utils'

export type SuccessCallback<T> = (res: T) => void
export type FailureCallback = (err: Error) => void

export interface XhrGetOptions<T> {
  url: string
  success?: SuccessCallback<T>
  fail?: FailureCallback
  withCredentials?: boolean
  getResponseText?: (responseText: XMLHttpRequest['responseText']) => void
}
export interface XhrPostOptions<T> {
  url: string
  data?: string
  success?: SuccessCallback<T>
  fail?: FailureCallback
}

interface XhrTransportOptions<T> {
  url: string
  data?: string
  success?: SuccessCallback<T>
  fail?: FailureCallback
  getResponseText?: (responseText: XMLHttpRequest['responseText']) => void
  withCredentials?: boolean
}
const request = <T>(
  method: 'GET' | 'POST',
  options: XhrTransportOptions<T>,
  XMLHttpRequest: typeof window['XMLHttpRequest'],
) => {
  const { url, data, success = noop, fail = noop, getResponseText = noop, withCredentials = false } = options

  const xhr = new XMLHttpRequest()
  xhr.withCredentials = withCredentials
  xhr.open(method, url, true)
  xhr.setRequestHeader('Content-Type', 'application/json')
  xhr.onload = function () {
    getResponseText?.(this.responseText)
    try {
      if (this.responseText) {
        const result = JSON.parse(this.responseText)
        success(result)
      } else {
        success({} as T)
      }
    } catch (e) {
      fail(e)
    }
  }
  xhr.onerror = function () {
    fail(new Error('Network request failed'))
  }
  xhr.onabort = function () {
    /* istanbul ignore next */
    fail(new Error('Network request aborted'))
  }
  xhr.send(data)
}

export const getXhrTransport = () => {
  const XMLHttpRequest = getDefaultXMLHttpRequest()
  return XMLHttpRequest
    ? {
        get(options: XhrGetOptions<any>) {
          request('GET', options, XMLHttpRequest)
        },
        post(options: XhrPostOptions<any>) {
          request('POST', options, XMLHttpRequest)
        },
      }
    : {
        get: noop,
        post: noop,
      }
}
