import { initSubjectInGlobal, WithSubject } from '@slardar/sdk-template'

import { loadSubject } from '../../collector/page'
import { resourceSubject } from '../../collector/pref'
import { resourceGetterWithContext, ResourceProps } from '../../monitors/resource'
import { WebClient } from '../../types'

import { getPluginConfig } from './utils'

export const RESOURCE_MONITOR_PLUGIN_NAME = 'resource'

export type ResourceMonitorConfig = ResourceProps

const defaultConfig: ResourceMonitorConfig = {
  ignoreUrls: [],
  slowSessionThreshold: 4000,
}
export interface ResourceMonitorPluginConfig {
  [RESOURCE_MONITOR_PLUGIN_NAME]?: Partial<ResourceMonitorConfig> | boolean
}

export function ResourceMonitorPlugin<C extends WithSubject<WebClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, RESOURCE_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    resourceGetterWithContext(
      client.report.bind(client),
      tearDownGroup,
      [initSubjectInGlobal(client, loadSubject), () => initSubjectInGlobal(client, resourceSubject)],
      config,
    )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
  })
}
