/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { WebReportEvent } from '../../types'
import {
  applyPerformance,
  applyMutationObserver,
  applyPerformanceObserver,
  getDefaultBrowser,
  getDefaultPerformance,
  getDefaultDocument,
  getDefaultMutationObserver,
  getDefaultPerformanceObserver,
  onPageUnload,
  applyRequestIdleCallback,
  now,
  onDOMContentLoaded,
} from '../../utils'

import { BlankScreenMonitorConstructor, BlankRelatedErrorInfo } from './type'
import {
  screenshot,
  measure,
  MAX_RELATED_TIME,
  blankErrorCompare,
  MAX_RENDER_TIME,
  DEFAULT_CHECK_TIME,
  MAX_CHECK_TIME,
  isSkipBeforeCheck,
} from './util'

export const BlankScreenMonitor: BlankScreenMonitorConstructor = (
  win = getDefaultBrowser(),
  document = getDefaultDocument(),
  performanceObserver = getDefaultPerformanceObserver(),
  mutationObserver = getDefaultMutationObserver(),
  performance = getDefaultPerformance(),
) => {
  if (!document || !win) {
    return
  }

  return (
    { threshold, screenshot: takeScreenshot, rootSelector, autoDetect, ssUrl, quality, mask, partialShot },
    cb,
  ) => {
    const requestIdleCallback = applyRequestIdleCallback(win)
    const [, relativeNow] = applyPerformance(performance)
    let blankInfo: [timestamp: number, score: number] | undefined
    let errorInfo: BlankRelatedErrorInfo | undefined
    let otherReportedTimestamp = 0
    let reported = false
    let timer: number

    const pureReport = (base64?: string) => {
      blankInfo &&
        cb &&
        cb({
          ev_type: 'blank_screen',
          payload: {
            timestamp: blankInfo[0],
            score: blankInfo[1],
            screenshot: base64,
            error: errorInfo,
          },
        })
    }

    const report = () => {
      if (!blankInfo || reported) {
        return
      }
      reported = true
      tearDown()

      takeScreenshot
        ? screenshot({
            cb: pureReport,
            screenshotUrl: ssUrl,
            window: win,
            document,
            mask,
            partialShot,
            quality,
            rootSelector,
          })
        : pureReport()
    }

    const applyOtherReport = (ev: WebReportEvent) => {
      if (reported || isSkipBeforeCheck(ev)) return
      otherReportedTimestamp = now()
      if (errorInfo && otherReportedTimestamp - errorInfo.timestamp > MAX_RELATED_TIME) {
        errorInfo = undefined
      }
      errorInfo = blankErrorCompare(errorInfo, ev)
    }

    const applyCheck = (cb: () => void) => {
      let checkTimer: number
      let checkTimestamp: number

      return () => {
        if (checkTimer) {
          return
        }
        checkTimestamp = now()
        checkTimer = win.setTimeout(
          () => {
            checkTimer = 0
            if (otherReportedTimestamp > checkTimestamp) {
              return
            }
            cb()
          },
          relativeNow() > MAX_RENDER_TIME ? DEFAULT_CHECK_TIME : MAX_CHECK_TIME,
        )
      }
    }

    const checkAndReport = applyCheck(report)

    onPageUnload(() => {
      !reported && errorInfo && pureReport()
    })

    const schedule = () => {
      timer && clearTimeout(timer)
      timer = win.setTimeout(() => {
        requestIdleCallback(() => {
          win.requestAnimationFrame(() => {
            // eslint-disable-next-line compat/compat
            const element = rootSelector ? document.querySelector(rootSelector) : document.body
            if (!element) {
              return
            }
            const score = measure(element, 0, 0, threshold)
            if (score < threshold) {
              blankInfo = [now(), score]
              checkAndReport()
            } else {
              blankInfo = undefined
            }
          })
        })
      }, 1000)
    }

    const tearDown = () => {
      clearTimeout(timer)
      mutDisconnect && mutDisconnect()
      perfDisconnect && perfDisconnect()
    }

    const [mutObserve, mutDisconnect] = applyMutationObserver(mutationObserver, schedule)
    const [perfObserve, perfDisconnect] = applyPerformanceObserver(
      performanceObserver,
      (_a, _i, arr) => timer && arr.length > 1 && schedule(),
    )

    if (autoDetect)
      // start observe blank screen after DOMContentLoaded
      onDOMContentLoaded(() => {
        mutObserve(getDefaultDocument()?.body, { subtree: true, childList: true })
        perfObserve('longtask', 'resource')
        schedule()
      })

    return [tearDown, applyOtherReport, schedule]
  }
}
