import { WebReportEvent, PerformanceLongTaskReport, CommonContext } from '../../types'
import { PerformancePayload } from '../../types/thrift/browser/collector_v2/performance'
import { PerformanceLongtask } from '../../types/thrift/browser/collector_v2/performance_longtask'
import { now } from '../../utils'
import { initMetric } from '../performance/util'

export const SINGLE_METRIC_EV_TYPE = 'performance'

export const LONG_TASK_EV_TYPE = 'performance_longtask'

export const applySendOnceAndTearDown = <T>(
  wrapPerformanceMetric: (metric: T) => WebReportEvent,
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
) => {
  let hasInvoked = false
  return (metric: T) => {
    tearDownGroup.length &&
      tearDownGroup.forEach((v) => {
        v()
      })
    tearDownGroup.length = 0
    if (hasInvoked) return
    hasInvoked = true
    report && report(wrapPerformanceMetric(metric))
  }
}

export const wrapPerformanceMetric = (
  metric: PerformancePayload,
  overrides?: Partial<CommonContext>,
): WebReportEvent => ({
  ev_type: SINGLE_METRIC_EV_TYPE,
  payload: metric,
  overrides: overrides,
})

export const wrapLongtask = (entry: PerformanceLongtask): PerformanceLongTaskReport => ({
  ev_type: LONG_TASK_EV_TYPE,
  payload: {
    type: 'perf',
    longtasks: [entry],
  },
})

export const applyReportMetric = (report: (ev: WebReportEvent) => void) => {
  return (metric: PerformancePayload, overrides?: Partial<CommonContext>) => {
    report(wrapPerformanceMetric(metric, overrides))
  }
}

export const SPA_LOAD_METRIC_NAME = 'spa_load'
export const getSPA = (report: (ev: WebReportEvent) => void) => {
  let startTime = 0
  const metric = initMetric(SPA_LOAD_METRIC_NAME, 0)

  const invokeCb = (load_time: number) => {
    metric.value = load_time
    report && report(wrapPerformanceMetric(metric))
  }

  const performanceInit = () => {
    startTime = now()
  }
  const performanceSend = () => {
    invokeCb(now() - startTime)
    startTime = 0
  }

  return [performanceInit, performanceSend]
}
