import { WithSubject, initSubjectInGlobal } from '@slardar/sdk-template'

import { fetchSubject, XHR_SUBJECT_NAME, applyXHR } from '../../collector/http'
import { ActionType, ActionMonitorConfig, StartAction, actionGetterWithFetchObserver } from '../../monitors/action'
import { WebClient, WebReportEvent } from '../../types'
import { getDefaultDocument, getDefaultBrowser } from '../../utils'

import { getPluginConfig } from './utils'

export const ACTION_MONITOR_PLUGIN_NAME = 'action'

export { ActionMonitorConfig } from '../../monitors/action'

export type WithAction<C extends WebClient> = C & {
  startAction?: StartAction
}
export interface ActionMonitorPluginConfig {
  [ACTION_MONITOR_PLUGIN_NAME]?: boolean | Partial<ActionMonitorConfig>
}

const defaultConfig: ActionMonitorConfig = {
  types: [ActionType.CLICK],
}

export function ActionMonitorBasePlugin<C extends WithSubject<WebClient>>(
  client: C,
  config: ActionMonitorConfig = defaultConfig,
) {
  const tearDownGroup: (() => void)[] = []
  const document = getDefaultDocument()
  const window = getDefaultBrowser()
  if (!document || !window) return

  const [applyOtherReport, startAction] = actionGetterWithFetchObserver(
    client.report.bind(client),
    tearDownGroup,
    [
      initSubjectInGlobal(client, [XHR_SUBJECT_NAME, applyXHR(XMLHttpRequest && XMLHttpRequest.prototype)]),
      initSubjectInGlobal(client, fetchSubject),
    ],
    config,
  )
  const listenOtherReport = (ev: WebReportEvent) => {
    const actionId = applyOtherReport(ev)
    actionId && ev.extra && (ev.extra.action_id = actionId)
    return ev
  }

  client.on('report', listenOtherReport)

  client.provide('startAction', startAction)

  client.on('beforeDestroy', () => {
    tearDownGroup.forEach((e) => e())
    client.off('report', listenOtherReport)
  })
}

export function ActionMonitorPlugin<C extends WithSubject<WebClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, ACTION_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }
    ActionMonitorBasePlugin(client, config)
  })
}
