import { WithSubject, initSubjectInGlobal } from '@slardar/sdk-template'

import { errorSubject, promiseRejectionSubject } from '../../collector/error'
import { jsErrorGetterWithContext, JsErrorConfig, ReportJsError } from '../../monitors/js-error'
import { WebClient } from '../../types'

import { WithBreadcrumb } from './breadcrumb'
import { getPluginConfig } from './utils'

export { JS_ERROR_EV_TYPE } from '../../monitors/js-error'
export { normalizeUnknownError } from '../../monitors/js-error/util'

export const JS_ERROR_MONITOR_PLUGIN_NAME = 'jsError'

export type WithCaptureException<C extends WebClient> = C & {
  captureException?: ReportJsError
}

export type WithJSErrorPrecollect<C extends WebClient> = C & {
  pcErr?: (e: ErrorEvent) => void // global 预收集error
  pcRej?: (e: PromiseRejectionEvent) => void // global 预收集 unhandledrejection
}

export type JsErrorMonitorConfig = JsErrorConfig

const defaultConfig: JsErrorMonitorConfig = {
  ignoreErrors: [],
  onerror: true,
  onunhandledrejection: true,
  captureGlobalAsync: false,
  dedupe: true,
}
export interface JsErrorMonitorPluginConfig {
  [JS_ERROR_MONITOR_PLUGIN_NAME]?: Partial<JsErrorMonitorConfig> | boolean
}

export function JsErrorMonitorPlugin<C extends WithJSErrorPrecollect<WithBreadcrumb<WithSubject<WebClient>>>>(
  client: C,
) {
  client.on('init', () => {
    // stop precollect listener no matter what is configured
    window.removeEventListener('error', client.pcErr as EventListenerOrEventListenerObject, true)
    window.removeEventListener('unhandledrejection', client.pcRej as EventListenerOrEventListenerObject, true)

    const config = getPluginConfig(client, JS_ERROR_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    const reportJsError = jsErrorGetterWithContext(
      (ev) => {
        client.getBreadcrumbs && (ev.payload.breadcrumbs = client.getBreadcrumbs())
        client.report(ev)
      },
      tearDownGroup,
      [initSubjectInGlobal(client, errorSubject), initSubjectInGlobal(client, promiseRejectionSubject)],
      config,
    )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })

    client.provide('captureException', reportJsError)
  })
}
