import { observePerf } from '../../collector/pref'
import { getDefaultPerformanceObserver, getDefaultPerformance, applyPerformance } from '../../utils'
import { MetricEnum } from '../performance/type'
import { initMetric } from '../performance/util'

import { PerfMetricGetterWithoutContext } from './type'
import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

export const FI_ENTRY_NAME = 'first-input'
export const FID_METRIC_NAME = MetricEnum.fid

const getFID: PerfMetricGetterWithoutContext = (report, tearDownGroup) => {
  const PerformanceObserver = getDefaultPerformanceObserver()
  const performance = getDefaultPerformance()

  const metric = initMetric(FID_METRIC_NAME, 0)
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)

  if (!performance || !PerformanceObserver) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  const invokePaint = ({ processingStart, startTime }: PerformanceEventTiming) => {
    metric.value = processingStart - startTime
    sendOnceAndTearDown(metric)
  }

  const [, , getEntriesByType] = applyPerformance(performance)
  const paint = getEntriesByType(FI_ENTRY_NAME)[0] as PerformanceEventTiming | undefined

  if (paint) {
    invokePaint(paint)
    return
  }

  tearDownGroup.push(observePerf(PerformanceObserver, invokePaint, [FI_ENTRY_NAME]))
}

export const FID: [MetricEnum, PerfMetricGetterWithoutContext] = [FID_METRIC_NAME, getFID]
