import { SafeObservable } from '@slardar/sdk-template'

import { HiddenNext, UnloadNext, observePerf, PVNext } from '../../collector'
import { WebReportEvent } from '../../types'
import { getDefaultPerformanceObserver, getDefaultPerformance, applyPerformance } from '../../utils'
import { MetricEnum } from '../performance/type'
import { initMetric } from '../performance/util'

import { PerfMetricGetterWithHiddenContext } from './type'
import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

export const FCP_ENTRY_NAME = 'first-contentful-paint'
export const FP_ENTRY_NAME = 'first-paint'
export const FP_METRIC_NAME = MetricEnum.fp
export const FCP_METRIC_NAME = MetricEnum.fcp
const Paint_Type = 'paint'

const getFP: PerfMetricGetterWithHiddenContext = (report, tearDownGroup, context) => {
  return getPaint(FP_ENTRY_NAME, FP_METRIC_NAME, report, tearDownGroup, context)
}

const getFCP: PerfMetricGetterWithHiddenContext = (report, tearDownGroup, context) => {
  return getPaint(FCP_ENTRY_NAME, FCP_METRIC_NAME, report, tearDownGroup, context)
}

export const getPaint = (
  entryName: string,
  metricName: string,
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  [hiddenObserver, unloadObserver]: [SafeObservable<HiddenNext>, SafeObservable<UnloadNext>, SafeObservable<PVNext>],
) => {
  const PerformanceObserver = getDefaultPerformanceObserver()
  const performance = getDefaultPerformance()

  const metric = initMetric(metricName, 0)
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)

  if (!performance || !PerformanceObserver) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  const invokePaint = ({ startTime }: PerformanceEntry) => {
    metric.value = startTime
    sendOnceAndTearDown(metric)
  }

  const [, , , , getEntriesByName] = applyPerformance(performance)
  const paint = getEntriesByName(entryName)[0]

  if (paint) {
    invokePaint(paint)
    return
  }

  tearDownGroup.push(
    observePerf(
      PerformanceObserver,
      (entry: PerformanceEntry) => {
        entry.name === entryName && invokePaint(entry)
      },
      [Paint_Type],
    ),
  )

  tearDownGroup.push(
    hiddenObserver[0](() => {
      metric.isSupport = false
      sendOnceAndTearDown(metric)
    }),
  )

  tearDownGroup.push(
    unloadObserver[0](() => {
      metric.isBounced = true
      sendOnceAndTearDown(metric)
    }),
  )
}

export const FP: [MetricEnum, PerfMetricGetterWithHiddenContext] = [FP_METRIC_NAME, getFP]
export const FCP: [MetricEnum, PerfMetricGetterWithHiddenContext] = [FCP_METRIC_NAME, getFCP]
