import { WithSubject, SubjectMap } from '@slardar/sdk-template'

import { WebClient } from '../types'

import { getDefaultBrowser } from './defaults'

export type SelfError = [Error, (Record<string, string> | undefined)?]

export interface LoadedPlugin {
  name: string
  version: string
  apply: PluginApplyFn
}

export interface PluginApplyFn {
  (client: WithSubject<WebClient>): void
}

export type SlardarRegistry = {
  plugins: LoadedPlugin[]
  errors: SelfError[] & {
    observe?: (f: (v: SelfError) => void) => void
  }
  subject: SubjectMap
}

// 获取全局注册表
export const getGlobalRegistry = (global?: Record<string, any>) => {
  if (!global) return

  if (!global.__SLARDAR_REGISTRY__) {
    global.__SLARDAR_REGISTRY__ = {
      Slardar: {
        plugins: [],
        errors: [],
        subject: {},
      },
    }
  }
  return global.__SLARDAR_REGISTRY__.Slardar as SlardarRegistry
}

export const reportSelfError = (...errorInfo: SelfError) => {
  const registry = getGlobalRegistry(getDefaultBrowser())
  if (!registry) return
  if (!registry.errors) {
    registry.errors = []
  }
  registry.errors.push(errorInfo)
}
