import { WebClient, WebReportEvent, CapturedContext } from '../types'

import { now } from './date'
import { getLocationUrl } from './url'

export const captureCurrentContext = <C extends WebClient>(client: C) => {
  const capturedContext: CapturedContext = {
    url: getLocationUrl(),
    timestamp: now(),
  }
  const config = client.config()
  if (config?.pid) {
    capturedContext.pid = config.pid
  }
  if (client?.['context']) {
    capturedContext.context = client['context'].toString()
  }
  return capturedContext
}

// only works for sync report
// async report won't trigger 'report' immediately, es.g. sri
export const syncReportWithCapturedContext =
  <C extends WebClient>(client: C, ctx: CapturedContext) =>
  (fn: () => void) => {
    const inject = (ev: WebReportEvent) => {
      ev.overrides = ctx
      return ev
    }
    client.on('report', inject as any)
    fn()
    client.off('report', inject as any)
  }
