import { SafeObservable, applyRecord, arrayIncludes, uuid } from '@slardar/sdk-template'

import { XHRNext, FetchNext } from '../../collector/http'
import { ActionPayload, WebReportEvent } from '../../types'
import { applyPerformance, applyMutationObserver, now as absoluteNow } from '../../utils'

import { waitActionComplete, getDomPath, getActionName } from './util'

export const enum ActionType {
  CLICK = 'click',
}

const ACTION_EV_TYPE = 'action'
const HTTP_EV_TYPE = 'http'
const LONGTASK_RV_TYPE = 'performance_longtask'
const JS_ERROR_EV_TYPE = 'js_error'
const RESOURCE_EV_TYPE = 'resource'
const RELATED_EV_TYPES = [LONGTASK_RV_TYPE, HTTP_EV_TYPE, JS_ERROR_EV_TYPE, RESOURCE_EV_TYPE]
const SUPPORT_TYPES = [ActionType.CLICK]
const EVENT_LISTENER_OPTIONS = { capture: true }

export const DEFAULT_ATTR_NAME = 'data-apm-action'

export interface ActionMonitorConfig {
  types: string[] | false
  // ignore association for each of action
  pure?: boolean
}
export interface StartAction {
  (name: string, type: string): void
}
export interface ApplyOtherReport {
  (ev: WebReportEvent): string | undefined
}

type ActionGetterWithContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<XHRNext>, SafeObservable<FetchNext>],
  config: ActionMonitorConfig,
) => [ApplyOtherReport, StartAction]

/**
 * 劫持 XHR 和 fetch，监听当前页面未完成的 请求数量
 */
export const applyIncompleteReq = (
  tearDownGroup: (() => void)[],
  [xhrObserver, fetchObserver]: [SafeObservable<XHRNext>, SafeObservable<FetchNext>],
  notify: () => void,
) => {
  const [incompleteReq, before, after] = applyRecord<number, number>()

  let uniqId = 0
  tearDownGroup.push(
    xhrObserver[0](([_method]) => {
      const requestId = (uniqId += 1)
      before(requestId, absoluteNow())
      notify()
      return () => {
        after(requestId)
        notify()
      }
    }),
  )

  tearDownGroup.push(
    fetchObserver[0](() => {
      const requestId = (uniqId += 1)
      before(requestId, absoluteNow())
      notify()
      return () => {
        after(requestId)
        notify()
      }
    }),
  )
  return incompleteReq
}

export const actionGetterWithFetchObserver: ActionGetterWithContext = (
  report,
  tearDownGroup,
  [xhrObserver, fetchObserver],
  config,
) => {
  const { types, pure } = config
  let currentPayload: ActionPayload | null = null
  let requestStartAndEnd: number[] = []
  let longtaskDuration = 0

  const [, now] = applyPerformance(performance)
  // 结算
  const reportActionIfAllowed = () => {
    // 不存在action上下文 || 未收集到相关payload || 依然有进行中的请求
    if (
      !currentPayload ||
      (!pure && !Object.keys(currentPayload.metrics!).length) ||
      Object.keys(incompleteReq).length
    ) {
      return
    }
    // longtask duration
    currentPayload.metrics![LONGTASK_RV_TYPE] && (currentPayload.metrics![LONGTASK_RV_TYPE].duration = longtaskDuration)

    // request duration
    const requestDuration =
      requestStartAndEnd.length > 0 ? Math.max(...requestStartAndEnd) - Math.min(...requestStartAndEnd) : 0
    currentPayload.metrics![HTTP_EV_TYPE] && (currentPayload.metrics![HTTP_EV_TYPE].duration = requestDuration)

    // end time
    const actionDuration = now() - currentPayload.start_time
    report({
      ev_type: ACTION_EV_TYPE,
      payload: {
        ...currentPayload,
        duration: actionDuration,
        frontend_time: actionDuration - requestDuration,
      },
    })

    // 上报后重置数据
    resetAction()
  }

  const resetAction = () => {
    complete()
    currentPayload = null
    requestStartAndEnd = []
    longtaskDuration = 0
  }

  const [start, notify, complete] = waitActionComplete(reportActionIfAllowed, resetAction, window)

  const incompleteReq = applyIncompleteReq(tearDownGroup, [xhrObserver, fetchObserver], notify)

  const [observe, disconnect] = applyMutationObserver(MutationObserver, notify)
  observe()
  tearDownGroup.push(disconnect)

  // 补充数据
  const applyOtherReport = (ev: WebReportEvent) => {
    if (!currentPayload) {
      return
    }
    if (!arrayIncludes(RELATED_EV_TYPES, ev.ev_type)) {
      return
    }
    // 通知监听，有相关变更
    notify()

    if (ev.ev_type === HTTP_EV_TYPE) {
      if (!ev.payload.response.timing) {
        return
      }
      const { startTime, duration } = ev.payload.response.timing
      if (startTime < currentPayload.start_time) {
        return
      }
      requestStartAndEnd.push(startTime, startTime + duration)
    } else if (ev.ev_type === LONGTASK_RV_TYPE) {
      if (!ev.payload.longtasks.length || ev.payload.longtasks[0].startTime < currentPayload.start_time) {
        return
      }
      ev.payload.longtasks.forEach((e) => {
        longtaskDuration = longtaskDuration + e.duration
      })
      // eslint-disable-next-line sonarjs/no-collapsible-if
    } else if (ev.ev_type === RESOURCE_EV_TYPE) {
      if ((ev.payload as PerformanceResourceTiming).startTime < currentPayload.start_time) {
        return
      }
    }
    currentPayload.metrics![ev.ev_type] = {
      count: (currentPayload.metrics![ev.ev_type]?.count ?? 0) + 1,
    }
    return currentPayload.id
  }

  const initAction = (target: { name: string; path?: string }, type: string) => {
    // refresh action id in common
    const actionId = uuid()
    const [, now] = applyPerformance(performance)

    // init payload
    currentPayload = {
      start_time: now(),
      id: actionId,
      type,
      target: target,
      metrics: {},
    }
    start()
  }

  const handleEvent = (e: Event) => {
    reportActionIfAllowed()
    resetAction()

    if (!(e.target instanceof HTMLElement)) {
      return
    }

    const name = getActionName(e.target, DEFAULT_ATTR_NAME)
    if (!name) {
      return
    }
    initAction({ name, path: getDomPath(e.target) }, e.type)
  }

  types &&
    types.forEach((t) => {
      if (arrayIncludes(SUPPORT_TYPES, t)) {
        document.addEventListener(t, handleEvent, EVENT_LISTENER_OPTIONS)
        tearDownGroup.push(() => document.removeEventListener(t, handleEvent, EVENT_LISTENER_OPTIONS))
      }
    })

  const startAction = (name: string, type: string) => {
    reportActionIfAllowed()
    resetAction()
    initAction({ name }, type)
  }

  return [applyOtherReport, startAction]
}
