import { initSubjectInGlobal, WithSubject, initPrivateSubject } from '@slardar/sdk-template'

import { applyPV, applyFirstPV, FIRST_PV_SUBJECT_NAME, PV_SUBJECT_NAME } from '../../collector/client'
import { hiddenSubject, unloadSubject, loadSubject } from '../../collector/page'
import { longtaskSubject } from '../../collector/pref'
import { FP, FCP, LCP, FID, CLS, LONGTASK, TIMING, MPFID } from '../../monitors/performance-new'
import {
  wrapPerformanceMetric,
  getSPA,
  SINGLE_METRIC_EV_TYPE,
  LONG_TASK_EV_TYPE,
} from '../../monitors/performance-new/util'
import { defaultMetricContext } from '../../monitors/performance/util'
import { WebClient, PerformancePayload, CommonContext, WebReportEvent } from '../../types'

import { WithPerformancePrecollect, PERFORMANCE_MONITOR_PLUGIN_NAME, defaultPerformancePrecollect } from './performance'
import { getPluginConfig } from './utils'

type CustomPerformancePayload = Partial<Omit<PerformancePayload, 'name' | 'value'>> &
  Pick<PerformancePayload, 'name' | 'value'>

export function PerformanceMonitorPlugin<C extends WithPerformancePrecollect<WithSubject<WebClient>>>(client: C) {
  client.on('init', () => {
    const precollect = client.pp || defaultPerformancePrecollect

    // clear precollect observer no matter what is configured
    precollect.observer?.disconnect()

    const config = getPluginConfig(client, PERFORMANCE_MONITOR_PLUGIN_NAME, {})
    if (!config) return

    const hiddenObserver = initSubjectInGlobal(client, hiddenSubject)
    const unloadObserver = initSubjectInGlobal(client, unloadSubject)
    const loadObserver = initSubjectInGlobal(client, loadSubject)
    const longtaskObserver = initSubjectInGlobal(client, longtaskSubject)
    const tearDownGroup: (() => void)[][] = []

    let overrides: Partial<CommonContext> | undefined = undefined
    const firstPVObserver = initPrivateSubject(client, [FIRST_PV_SUBJECT_NAME, applyFirstPV(client)], -1)
    firstPVObserver[0]((o) => {
      overrides = o
    })()

    const reportMetric = (data: WebReportEvent) => {
      // every first screen metrics needs to be associated with the context when client init, but CLS and long task are not included
      const payloadWithOverrides =
        (data.ev_type === SINGLE_METRIC_EV_TYPE && (data.payload as PerformancePayload).name === CLS[0]) ||
        data.ev_type === LONG_TASK_EV_TYPE
          ? data
          : {
              ...data,
              overrides,
            }
      client.report(payloadWithOverrides)
    }

    const pvObserver = initPrivateSubject(client, [PV_SUBJECT_NAME, applyPV(client)])
    ;[FP, FCP, LCP, FID, CLS].forEach((cur) => {
      if (config[cur[0]] !== false) {
        const tearDown: (() => void)[] = []
        cur[1](reportMetric, tearDown, [hiddenObserver, unloadObserver, pvObserver])
        tearDownGroup.push(tearDown)
      }
    })
    ;[LONGTASK, TIMING, MPFID].forEach((cur) => {
      if (config[cur[0]] !== false) {
        const tearDown: (() => void)[] = []
        cur[1](reportMetric, tearDown, [loadObserver, unloadObserver, longtaskObserver])
        tearDownGroup.push(tearDown)
      }
    })

    // spa_load
    const [performanceInit, performanceSend] = getSPA(client.report.bind(client))

    client.provide('performanceInit', performanceInit)
    client.provide('performanceSend', performanceSend)

    // clear precollect data after consumed
    precollect.entries.length = 0

    // provide custom performance API
    const sendPref = (customMetric: CustomPerformancePayload) => {
      const payload = {
        ...defaultMetricContext,
        ...customMetric,
        isCustom: true,
      }
      client.report(wrapPerformanceMetric(payload))
    }
    client.provide('sendCustomPerfMetric', sendPref)

    client.on('beforeDestroy', () => {
      tearDownGroup
        .reduce((pre, cur) => {
          return pre.concat(cur)
        }, [])
        .forEach((fn) => fn())
      tearDownGroup.length = 0
    })
  })
}
