import { SafeObservable, applyRecord, arrayIncludes, noop } from '@slardar/sdk-template'

import { PVNext } from '../../collector/client'
import { XHRNext, FetchNext } from '../../collector/http'
import { LongtaskNext, ResourceNext, LONGTASK_TYPE } from '../../collector/pref'
import { WebReportEvent } from '../../types'
import { PerformanceLongtask } from '../../types/thrift/browser/collector_v2/performance_longtask'
import {
  applyPerformance,
  getDefaultMutationObserver,
  applyMutationObserver,
  now as absoluteNow,
  getDefaultPerformance,
} from '../../utils'
import { getFetchMethod } from '../http/fetch'
import { initMetric } from '../performance/util'

import { FCP_ENTRY_NAME } from './paint'
import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

const TTI_METRIC_NAME = 'tti'
const GET_METHOD = 'get'
const QUIET_TIME_LIMIT = 5000

export interface ResourceAndRequestItem {
  start: number
  end: number
}
export interface NEWPerformanceLongTask extends PerformanceLongtask {
  start: number
  end: number
}

interface PerformancePrecollect {
  entries: PerformanceEntry[]
  observer?: PerformanceObserver
}

type TTIPerfMetricGetterWithLoadContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [
    SafeObservable<XHRNext>,
    SafeObservable<FetchNext>,
    SafeObservable<LongtaskNext>,
    SafeObservable<ResourceNext>,
    SafeObservable<PVNext>,
  ],
  config: PerformancePrecollect | undefined,
) => void

/**
 * start 方法开启一个调度器，传入回调和调度时间
 * reschedule 重新以一个更长的时间调度
 * stop 停止调度
 */
export const applyScheduler = (now: typeof performance['now']) => {
  let timerActiveTime = -Infinity
  let timerId: number | undefined = undefined
  let callback: (() => void) | undefined

  const clearTime = () => window.clearTimeout(timerId)
  const reschedule = (futureTime: number) => {
    if (timerActiveTime > futureTime || !callback) {
      return
    }
    clearTime()
    timerId = window.setTimeout(callback, futureTime - now())
    timerActiveTime = futureTime
  }
  const start = (cb: () => void, futureTime: number) => {
    callback = cb
    reschedule(futureTime)
  }
  const stop = () => {
    clearTime()
    callback = undefined
  }

  return [start, stop, reschedule] as const
}

/**
 * Computes the time (in milliseconds since requestStart) that the network was
 * last known to have >2 requests in-flight.
 */
export const calcLastNet2Busy = (
  undoneReqStarts: number[],
  observedResReqs: ResourceAndRequestItem[],
  now: typeof performance['now'],
) => {
  if (undoneReqStarts.length > 2) {
    return now()
  }

  const enum ENDPOINT_TYPE {
    S,
    E,
  }
  const endpoints: [number, ENDPOINT_TYPE][] = []
  for (let i = 0; i < observedResReqs.length; i++) {
    endpoints.push([observedResReqs[i].start, ENDPOINT_TYPE.S], [observedResReqs[i].end, ENDPOINT_TYPE.E])
  }

  for (let i = 0; i < undoneReqStarts.length; i++) {
    endpoints.push([undoneReqStarts[i], ENDPOINT_TYPE.S])
  }

  endpoints.sort((a, b) => a[0] - b[0])

  let currentActive = undoneReqStarts.length

  for (let i = endpoints.length - 1; i >= 0; i--) {
    const [timestamp, type] = endpoints[i]
    switch (type) {
      case ENDPOINT_TYPE.S:
        currentActive--
        break
      case ENDPOINT_TYPE.E:
        currentActive++
        if (currentActive > 2) {
          return timestamp
        }
        break
    }
  }

  // If we reach here, we were never network 2-busy.
  return 0
}

export const getRequestTimes = (requestTimes: Record<number, number>) => {
  const keys = Object.keys(requestTimes)
  const result: number[] = []
  // eslint-disable-next-line @typescript-eslint/prefer-for-of
  for (let i = 0; i < keys.length; i++) {
    const val = requestTimes[keys[i]]
    if (typeof val === 'number') {
      result.push(val)
    }
  }
  return result
}

interface NodeWithChildren extends Node {
  children: NodeList
}

/**
 * 监听包含 ['img', 'script', 'iframe', 'link', 'audio', 'video', 'source'] 标签的 dom 变更
 */
export const observeResFetchMutations = (
  MutationObserver: typeof window.MutationObserver | null | undefined | false,
  callback: (mutation: MutationRecord) => void,
) => {
  const requestCreatingNodeNames = ['img', 'script', 'iframe', 'link', 'audio', 'video', 'source']

  function subtreeContainsNodeName(nodes: NodeList, nodeNames: string[]) {
    for (let i = 0; i < nodes.length; i++) {
      if (
        arrayIncludes(nodeNames, nodes[i].nodeName.toLowerCase()) ||
        ((nodes[i] as NodeWithChildren).children &&
          subtreeContainsNodeName((nodes[i] as NodeWithChildren).children, nodeNames))
      ) {
        return true
      }
    }
    return false
  }

  const [observe, disconnect] = applyMutationObserver(MutationObserver, (mutations) => {
    for (let i = 0; i < mutations.length; i++) {
      if (
        (mutations[i].type === 'childList' &&
          subtreeContainsNodeName(mutations[i].addedNodes, requestCreatingNodeNames)) ||
        (mutations[i].type === 'attributes' &&
          arrayIncludes(requestCreatingNodeNames, mutations[i].target.nodeName.toLowerCase()))
      ) {
        callback(mutations[i])
      }
    }
  })

  return [
    () =>
      observe(document, {
        attributes: true,
        childList: true,
        subtree: true,
        attributeFilter: ['href', 'src'],
      }),
    disconnect,
  ] as const
}

/**
 * Returns either a manually set min value or the time since
 * domContentLoadedEventEnd and navigationStart. If the
 * domContentLoadedEventEnd data isn't available, `null` is returned.
 * @return {number|null}
 */
export const getMinValue = (timing?: PerformanceTiming) => {
  const { domContentLoadedEventEnd, navigationStart = 0 } = timing || {}
  return domContentLoadedEventEnd ? domContentLoadedEventEnd - navigationStart : null
}

/**
 * Computes the TTI value...
 * @param {number} searchStart
 * @param {number} minValue
 * @param {number} lastKnownNetwork2Busy
 * @param {number} currentTime
 * @param {!Array<{start: (number), end: (number)}>} longTasks
 * @return {number|null}
 */
export const computeTTI = (
  searchStart: number,
  minValue: number,
  lastKnownNetwork2Busy: number,
  currentTime: number,
  longTasks: Array<{ start: number; end: number }>,
) => {
  // Have not reached network 2-quiet yet.
  if (currentTime - lastKnownNetwork2Busy < QUIET_TIME_LIMIT) {
    return null
  }

  const maybeFCI = longTasks.length === 0 ? searchStart : longTasks[longTasks.length - 1].end

  // Main thread has not been quiet for long enough.
  if (currentTime - maybeFCI < QUIET_TIME_LIMIT) {
    return null
  }

  return Math.max(maybeFCI, minValue)
}

const pushLongTaskIntoList = (entry: PerformanceEntry, list: NEWPerformanceLongTask[]) => {
  const { startTime, duration } = entry
  ;(entry as any).start = startTime
  ;(entry as any).end = startTime + duration
  list.push(entry as any as NEWPerformanceLongTask)
}

export const getLastBusyAndLongTasks =
  (
    tearDownGroup: (() => void)[],
    [xhrObserver, fetchObserver, longtaskObserver, resourceObserver, MutationObserver]: [
      SafeObservable<XHRNext>,
      SafeObservable<FetchNext>,
      SafeObservable<LongtaskNext>,
      SafeObservable<ResourceNext>,
      typeof window.MutationObserver | undefined,
    ],
    entries: PerformanceEntry[],
  ) =>
  (reschedule: (futureTime: number) => void, now: () => number): [NEWPerformanceLongTask[], () => number] => {
    const longTasks: NEWPerformanceLongTask[] = []
    const networkRequests: ResourceAndRequestItem[] = []
    const [incompleteInitReqStartTimes, before, after] = applyRecord<number, number>()

    entries.forEach((entry) => {
      if (entry.entryType === LONGTASK_TYPE[0]) {
        pushLongTaskIntoList(entry, longTasks)
      }
    })

    let uniqId = 0
    tearDownGroup.push(
      xhrObserver[0](([_method]) => {
        if ((_method || '').toLowerCase() !== GET_METHOD) return noop
        const requestId = (uniqId += 1)
        before(requestId, absoluteNow())
        return () => {
          after(requestId)
        }
      }),
    )

    tearDownGroup.push(
      fetchObserver[0](([req, options]) => {
        if (!window.Request || getFetchMethod(req, options, window.Request) !== GET_METHOD) return noop
        const requestId = (uniqId += 1)
        before(requestId, absoluteNow())
        return () => {
          after(requestId)
        }
      }),
    )

    const [observeRes, stopObserveRes] =
      (MutationObserver && observeResFetchMutations(MutationObserver, () => reschedule(now() + QUIET_TIME_LIMIT))) || []
    observeRes && observeRes()

    const getLastBusy = () => calcLastNet2Busy(getRequestTimes(incompleteInitReqStartTimes), networkRequests, now)

    stopObserveRes && tearDownGroup.push(stopObserveRes as () => void)

    tearDownGroup.push(
      longtaskObserver[0]((entry) => {
        pushLongTaskIntoList(entry, longTasks)
        const { startTime, duration } = entry
        // 遇到 longTask 则在 longTask 5秒后 checkTTI
        reschedule(startTime + duration + QUIET_TIME_LIMIT)
      }),
    )

    tearDownGroup.push(
      resourceObserver[0](({ fetchStart, responseEnd }) => {
        networkRequests.push({
          start: fetchStart,
          end: responseEnd,
        })
        // 遇到 resource 加载，则在最后一次 busy 5 秒后 checkTTI
        reschedule(getLastBusy() + QUIET_TIME_LIMIT)
      }),
    )

    tearDownGroup.push(() => {
      longTasks.length = 0
      networkRequests.length = 0
    })

    return [longTasks, getLastBusy]
  }

export const getTTI: TTIPerfMetricGetterWithLoadContext = (
  report,
  tearDownGroup,
  [xhrObserver, fetchObserver, longtaskObserver, resourceObserver, pvObserver],
  config,
) => {
  const metric = initMetric(TTI_METRIC_NAME, 0)
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)
  const performance = getDefaultPerformance()

  const { entries = [], observer: snippetObserver } = config || {}

  tearDownGroup.push(() => {
    snippetObserver && snippetObserver.disconnect()
    entries.length = 0
  })

  if (
    !window ||
    !XMLHttpRequest ||
    !performance ||
    !PerformanceObserver ||
    (PerformanceObserver.supportedEntryTypes &&
      !arrayIncludes((PerformanceObserver.supportedEntryTypes as string[]) || [], LONGTASK_TYPE[0]))
  ) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  const [timing, now, , , getEntriesByName] = applyPerformance(performance)

  const [startSchedule, stopSchedule, reschedule] = applyScheduler(now)
  const [longTasks, getLastBusy] = getLastBusyAndLongTasks(
    tearDownGroup,
    [xhrObserver, fetchObserver, longtaskObserver, resourceObserver, getDefaultMutationObserver()],
    entries,
  )(reschedule, now)

  tearDownGroup.push(stopSchedule)

  const checkTTI = (ttiCallback: (v: number) => void) => {
    const firstContentfulPaint = getEntriesByName(FCP_ENTRY_NAME)[0]
    const maybeFCI = computeTTI(
      (firstContentfulPaint ? firstContentfulPaint.startTime : getMinValue(timing)) || 0,
      getMinValue(timing) || 0,
      getLastBusy(),
      now(),
      longTasks,
    )

    if (!maybeFCI) return reschedule(now() + 1000)

    ttiCallback(maybeFCI)
  }

  const reportTTI = (value: number) => {
    metric.value = value
    sendOnceAndTearDown(metric)
  }

  const lastLongTask = longTasks[longTasks.length - 1]
  startSchedule(
    () => checkTTI(reportTTI),
    Math.max(getLastBusy() + QUIET_TIME_LIMIT, lastLongTask ? lastLongTask.end : 0),
  )

  tearDownGroup.push(
    pvObserver[0](() => {
      metric.isSupport = false
      sendOnceAndTearDown(metric)
    }),
  )
}
