import { AnyClient } from '../types'
import { Observer, createSafeObserver, SafeObservable } from '../utils/observable'

export type WithSubject<C extends AnyClient> = C & {
  initSubject: <T extends (args: any) => any>(args: [subjectKey: string, collector: Observer<T>]) => SafeObservable<T>
  getSubject: (key: string) => SafeObservable<any> | undefined
  privateSubject: SubjectMap
}

export type SubjectMap = { [key: string]: SafeObservable<any> | undefined }

export function SubjectPlugin(client: AnyClient, globalSubjectMap: SubjectMap | undefined) {
  const subjectMap: SubjectMap = globalSubjectMap || {}
  const initSubject = <T extends (args: any) => any>([subjectKey, observer]: [
    string,
    Observer<T>,
  ]): SafeObservable<T> => {
    if (!subjectMap[subjectKey])
      subjectMap[subjectKey] = createSafeObserver(observer, () => {
        subjectMap[subjectKey] = undefined
      })
    return subjectMap[subjectKey] as SafeObservable<T>
  }
  client.provide('initSubject', initSubject)
  client.provide('getSubject', (key: string) => subjectMap[key])
  client.provide('privateSubject', {})
}

/**
 * 由于 Observer 带有自动销毁机制，意味着subject 只支持同步注册并使用
 */
export function initSubjectInGlobal<T extends (args: any) => any>(
  client: WithSubject<AnyClient>,
  args: [subjectKey: string, observer: Observer<T>],
) {
  return client.initSubject(args)
}

/**
 * 此方法不会挂载到全局对象上，同时只支持同步注册并使用
 */
export function initPrivateSubject<T extends (args: any) => any>(
  client: WithSubject<AnyClient>,
  [subjectKey, observer]: [subjectKey: string, observer: Observer<T>],
  bufferTime?: number,
) {
  const subjectMap = client.privateSubject || {}
  if (!subjectMap[subjectKey])
    subjectMap[subjectKey] = createSafeObserver(
      observer,
      () => {
        subjectMap[subjectKey] = undefined
      },
      bufferTime,
    )
  return subjectMap[subjectKey] as SafeObservable<T>
}
