import { WithSubject, initPrivateSubject } from '@slardar/sdk-template'

import { FIRST_PV_SUBJECT_NAME, applyFirstPV } from '../../collector/client'
import { hashSubject, historySubject } from '../../collector/route'
import { pvGetterWithRouteObserver } from '../../monitors/pageview'
import { PageviewProps, SendPageview } from '../../monitors/pageview/type'
import { isManualMode } from '../../monitors/pageview/util'
import { WebClient } from '../../types'
import { now } from '../../utils'

import { getPluginConfig } from './utils'

export const PAGEVIEW_MONITOR_PLUGIN_NAME = 'pageview'

export type WithSendPageview<C extends WithSubject<WebClient>> = C & {
  sendPageview?: SendPageview
}

export type PageviewMonitorConfig = Omit<PageviewProps, 'onPidUpdate' | 'initPid'>

const defaultConfig: PageviewMonitorConfig = {
  sendInit: true,
  routeMode: 'history',
}
export interface PageviewMonitorPluginConfig {
  [PAGEVIEW_MONITOR_PLUGIN_NAME]?: Partial<PageviewMonitorConfig> | boolean
}

export function PageviewMonitorPlugin<C extends WithSubject<WebClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, PAGEVIEW_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config || !location) {
      return
    }
    const tearDownGroup: (() => void)[] = []

    const { routeMode } = config
    const [sendPageview] = pvGetterWithRouteObserver(
      client.report.bind(client),
      tearDownGroup,
      isManualMode(routeMode) ? [] : [client.initSubject(hashSubject), client.initSubject(historySubject)],
      {
        ...config,
        initPid: client.config()?.pid,
        onPidUpdate: (pid: string) => {
          client.set({ pid, viewId: `${pid}_${now()}`, actionId: undefined })
        },
      },
    )

    initPrivateSubject(client, [FIRST_PV_SUBJECT_NAME, applyFirstPV(client)], -1)

    client.on('config', () => {
      sendPageview(client.config()!.pid)
    })

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
    client.provide('sendPageview', sendPageview)
  })
}
