/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { id, Processor } from '@slardar/sdk-template'

import { Breadcrumb } from '../../types'
import { applyDomAndKeyPress, domBreadcrumb, triggerHandlers, now } from '../../utils'

import { OnMaxBreadcrumbs, PreBreadcrumb } from './type'

export { applyDomAndKeyPress, domBreadcrumb, triggerHandlers }

export const applyBreadcrumb = (
  maxBreadcrumbs = 20,
  onAddBreadcrumb: Processor<PreBreadcrumb> = id,
  onMaxBreadcrumbs: OnMaxBreadcrumbs = (bs, max) => bs.slice(-max),
) => {
  let breadcrumbs: Breadcrumb[] = []

  const addBreadcrumb = (breadcrumb: PreBreadcrumb) => {
    const processed = onAddBreadcrumb(breadcrumb)

    if (processed) {
      const mergedBreadcrumb = {
        ...breadcrumb,
        timestamp: breadcrumb.timestamp || now(),
      }

      breadcrumbs =
        maxBreadcrumbs >= 0 && breadcrumbs.length + 1 > maxBreadcrumbs
          ? onMaxBreadcrumbs([...breadcrumbs, mergedBreadcrumb], maxBreadcrumbs)
          : [...breadcrumbs, mergedBreadcrumb]
    }
  }

  return [() => breadcrumbs, addBreadcrumb] as const
}
