/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { ReportToReportEvent, WebClient, WebReport, WebReportEvent } from '../../types'
import { getLocationUrl, getConfig } from '../../utils'

export function getPluginConfig<C>(client: WebClient, pluginName: string, defaultConfig: C): C | false {
  const c = client.config()?.plugins[pluginName] as C | undefined
  return getConfig(c, defaultConfig)
}

export const reportOnInitCommonParams = (
  client: WebClient,
  overrides?: (ev: ReportToReportEvent<WebReport>) => WebReportEvent['overrides'],
) => {
  const clientConfig = client.config()!
  const common = {
    url: getLocationUrl(),
    pid: clientConfig.pid,
    view_id: clientConfig.viewId,
  }
  return (ev: ReportToReportEvent<WebReport>) => {
    client.report({
      ...ev,
      overrides: {
        ...common,
        ...((overrides && overrides(ev)) || {}),
      },
    })
  }
}
