/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { WebReportEvent, WebClient } from '../types'
import { getDefaultNetworkInformation, NetworkInformation } from '../utils'

export function getNetworkType(netInfo?: NetworkInformation) {
  return netInfo?.effectiveType || netInfo?.type || ''
}

export const InjectNetworkTypePlugin = (client: WebClient) => {
  const netInfo = getDefaultNetworkInformation()
  let network_type = getNetworkType(netInfo)
  if (netInfo) {
    netInfo.onchange = () => {
      network_type = getNetworkType(netInfo)
    }
  }

  client.on('report', (ev: WebReportEvent) => {
    return {
      ...ev,
      extra: {
        ...(ev.extra || {}),
        network_type,
      },
    }
  })
}
