/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { PerformanceMonitor } from '../../monitors/performance'
import { CLSMonitor } from '../../monitors/performance/cls-monitor'
import { LongtaskMonitor } from '../../monitors/performance/longtask-monitor'
import { SPAMonitor } from '../../monitors/performance/spa-monitor'
import { PerformanceInit, PerformanceSend } from '../../monitors/performance/spa-monitor/type'
import { PerformanceMonitorUserConfig, PerformancePrecollect } from '../../monitors/performance/type'
import { defaultMetricContext } from '../../monitors/performance/util'
import { PerformancePayload, WebClient, PerformanceLongtaskPayload } from '../../types'
import { applyMonitor, onPageUnload, getLocationUrl } from '../../utils'

import { getPluginConfig, reportOnInitCommonParams } from './utils'

export { PerformancePrecollect }

export const PERFORMANCE_MONITOR_PLUGIN_NAME = 'performance'

type CustomPerformancePayload = Partial<Omit<PerformancePayload, 'name' | 'value'>> &
  Pick<PerformancePayload, 'name' | 'value'>

export type WithSendPerformance<C extends WebClient> = C & {
  sendCustomPerfMetric?: (metric: CustomPerformancePayload) => void
  performanceInit?: PerformanceInit
  performanceSend?: PerformanceSend
}

export type WithPerformancePrecollect<C extends WebClient> = C & {
  pp?: PerformancePrecollect
}

export const defaultPerformancePrecollect: PerformancePrecollect = {
  entries: [],
  observer: undefined,
}

const defaultConfig: PerformanceMonitorUserConfig = {
  fp: true,
  fcp: true,
  fid: true,
  mpfid: true,
  lcp: true,
  cls: true,
  timing: true,
  longtask: true,
}
export interface PerformanceMonitorPluginConfig {
  [PERFORMANCE_MONITOR_PLUGIN_NAME]?: Partial<PerformanceMonitorUserConfig> | boolean
}

export function PerformanceMonitorPlugin<C extends WithPerformancePrecollect<WebClient>>(client: C) {
  client.on('init', () => {
    const precollect = client.pp || defaultPerformancePrecollect

    // clear precollect observer no matter what is configured
    precollect.observer?.disconnect()

    const config = getPluginConfig(client, PERFORMANCE_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    applyMonitor(PerformanceMonitor, { ...config, precollect }, reportOnInitCommonParams(client))

    const sendPrefMetric = (metric: PerformancePayload) => {
      client.report({ ev_type: 'performance', payload: metric })
    }

    // spa_load
    const [performanceInit, performanceSend] = applyMonitor(SPAMonitor, 0, sendPrefMetric)

    client.provide('performanceInit', performanceInit)
    client.provide('performanceSend', performanceSend)

    // longtasks
    if (config.longtask) {
      const sendPerformanceLongtask = (longtaskPayload: PerformanceLongtaskPayload) => {
        client.report({ ev_type: 'performance_longtask', payload: longtaskPayload })
      }

      const [longtaskTearDown] = applyMonitor(LongtaskMonitor, { precollect }, sendPerformanceLongtask)
      client.on('beforeDestroy', longtaskTearDown)
    }

    // cls
    if (config.cls) {
      let originUrl = getLocationUrl()
      const sendCLS = (metric: PerformancePayload) => {
        client.report({ ev_type: 'performance', payload: metric, overrides: { url: originUrl } })
      }

      const [countAndClear, tearDown] = applyMonitor(CLSMonitor, [precollect], sendCLS)
      client.on('beforeConfig', (newConfig) => {
        if (newConfig.viewId && newConfig.viewId !== client.config()?.viewId) {
          countAndClear()
          originUrl = getLocationUrl()
        }
      })

      client.on('beforeDestroy', tearDown)
      onPageUnload(countAndClear)
    }

    // clear precollect data after consumed
    precollect.entries.length = 0

    // provide custom performance API
    const invokeCustomMetricCb = (customMetric: CustomPerformancePayload) => {
      const payload = {
        ...defaultMetricContext,
        ...customMetric,
        isCustom: true,
      }
      sendPrefMetric(payload)
    }

    client.provide('sendCustomPerfMetric', invokeCustomMetricCb)
  })
}
