import { Observer, noop } from '@slardar/sdk-template'

import { WebClient, WebConfig } from '../types'
import { Common } from '../types/thrift/browser/collector_v2/common'
import { getLocationUrl } from '../utils/url'

const getOverrides = (config: Partial<WebConfig>): Partial<Common> => {
  return {
    pid: config.pid,
    view_id: config.viewId,
    url: getLocationUrl(),
  }
}

export const PV_SUBJECT_NAME = 'view_0'

export type PVNext = (args: Partial<Common>) => void

/**
 * 由于跟当前实例强相关 所以不能挂在global上
 */
export const applyPV = (client: WebClient): Observer<PVNext> => {
  return (next, tearDown) => {
    let currentOverrides = getOverrides(client.config()!)
    const handleConfig = (newConfig: Partial<WebConfig>) => {
      if (newConfig.viewId && newConfig.viewId !== client.config()?.viewId) {
        next(currentOverrides)
        currentOverrides = getOverrides(newConfig)
      }
    }
    client.on('beforeConfig', handleConfig)
    tearDown(() => {
      client.off('beforeConfig', handleConfig)
    })
  }
}

export const FIRST_PV_SUBJECT_NAME = 'f_view_0'
/**
 * 由于跟当前实例强相关 所以不能挂在global上
 */
export const applyFirstPV = (client: WebClient): Observer<PVNext> => {
  return (_, tearDown) => {
    const firstOverrides: Partial<Common> | undefined = getOverrides(client.config()!)
    tearDown(noop, (subscriber) => {
      firstOverrides && subscriber(firstOverrides!)
    })
  }
}
