import { BlankScreenMonitor } from '../../monitors/blank-screen'
import { BlankScreenMonitorConfig, DetectBlankScreen } from '../../monitors/blank-screen/type'
import { DEFAULT_THRESHOLD, DEFAULT_T0_DATA_URL_QUALITY } from '../../monitors/blank-screen/util'
import { WebClient, WebReportEvent } from '../../types'
import { applyMonitor, getConfig } from '../../utils'

import { getPluginConfig } from './utils'

export { BlankScreenMonitorConfig } from '../../monitors/blank-screen/type'

export const BLANK_SCREEN_MONITOR_PLUGIN_NAME = 'blankScreen'

export type WithDetectBlankScreen<C extends WebClient> = C & {
  detectBlankScreen?: DetectBlankScreen
}
export interface BlankScreenMonitorPluginConfig {
  [BLANK_SCREEN_MONITOR_PLUGIN_NAME]?: Partial<BlankScreenMonitorConfig> | boolean
}

function BlankScreenMonitorBasePlugin<C extends WebClient>(
  client: C,
  defaultConfig: BlankScreenMonitorConfig,
  overrideConfig?: Partial<BlankScreenMonitorConfig>,
) {
  client.on('init', () => {
    const config =
      getPluginConfig(client, BLANK_SCREEN_MONITOR_PLUGIN_NAME, defaultConfig) ||
      getConfig(overrideConfig, defaultConfig)
    if (!config) {
      return
    }
    const [tearDown, applyOtherReport, detectBlankScreen] = applyMonitor(
      BlankScreenMonitor,
      config,
      client.report.bind(client),
    )
    const listenOtherReport = (ev: WebReportEvent) => {
      applyOtherReport(ev)
      return ev
    }

    client.on('report', listenOtherReport)

    client.on('beforeDestroy', () => {
      tearDown()
      client.off('report', listenOtherReport)
    })

    client.provide('detectBlankScreen', detectBlankScreen)
  })
}

const defaultByteConfig: BlankScreenMonitorConfig = {
  autoDetect: true,
  threshold: DEFAULT_THRESHOLD,
  screenshot: true,
  ssUrl: 'https://sf16-short-va.bytedapm.com/slardar/fe/sdk-web/screenshot.min.js',
  mask: false,
  partialShot: false,
  quality: DEFAULT_T0_DATA_URL_QUALITY,
}

export function BlankScreenMonitorPlugin<C extends WebClient>(
  client: C,
  overrideConfig?: Partial<BlankScreenMonitorConfig>,
) {
  BlankScreenMonitorBasePlugin(client, defaultByteConfig, overrideConfig)
}

const defaultVolConfig: BlankScreenMonitorConfig = {
  autoDetect: true,
  threshold: DEFAULT_THRESHOLD,
  screenshot: true,
  ssUrl: 'https://apm.volccdn.com/mars-web/apmplus/web/html2canvas.min.js',
  mask: false,
  partialShot: false,
  quality: DEFAULT_T0_DATA_URL_QUALITY,
}

export function BlankScreenMonitorVolPlugin<C extends WebClient>(
  client: C,
  overrideConfig?: Partial<BlankScreenMonitorConfig>,
) {
  BlankScreenMonitorBasePlugin(client, defaultVolConfig, overrideConfig)
}
