import { hookMethodDangerously, Observer, NextLink, ParametersForFn } from '@slardar/sdk-template'

import { now, getDefaultBrowser } from '../utils'

export const XHR_SUBJECT_NAME = 'xhr_0'

export type XHRNext = ([_method, _url, _start, xhr]: [
  _method: string,
  _url: string,
  _start: number,
  xhr: NewXMLHttpRequest,
]) => (xhr: NewXMLHttpRequest) => void

export interface NewXMLHttpRequest extends XMLHttpRequest {
  _url: string
  _method: string
  _start: number
  _data: Parameters<XMLHttpRequest['send']>[0]
  _reqHeaders?: Record<string, string>
}

const hookOpen = (open: XMLHttpRequest['open']) =>
  function (this: NewXMLHttpRequest, ...openOptions: Parameters<XMLHttpRequest['open']>) {
    // @ts-expect-error
    ;[this._method, this._url] = openOptions
    return open.apply(this, openOptions)
  } as XMLHttpRequest['open']

const hookSetHeader = (setRequestHeader?: XMLHttpRequest['setRequestHeader']) =>
  function (this: NewXMLHttpRequest, ...setOptions: Parameters<XMLHttpRequest['setRequestHeader']>) {
    this._reqHeaders = this._reqHeaders || {}
    const [name, value] = setOptions
    this._reqHeaders[name] = value
    return setRequestHeader && setRequestHeader.apply(this, setOptions)
  }

const hookOnreadystatechange = (xhr: NewXMLHttpRequest, endNext: ReturnType<NextLink<XHRNext>>) =>
  hookMethodDangerously(
    xhr,
    'onreadystatechange',
    (origin) =>
      function (this: XMLHttpRequest, ...ev: [ev: Event]) {
        // @ts-expect-error
        this.readyState === 4 && endNext(xhr)
        return origin && origin.apply(this, ev)
      },
  )

const hookSend = (
  send: XMLHttpRequest['send'],
  next: (args: ParametersForFn<XHRNext>) => ReturnType<NextLink<XHRNext>>,
) => {
  return function (this: NewXMLHttpRequest, ...sendOptions: Parameters<XMLHttpRequest['send']>) {
    this._start = now()
    this._data = sendOptions?.[0]
    const endNext = next([this._method, this._url, this._start, this])
    hookOnreadystatechange(this, endNext)()
    return send.apply(this, sendOptions)
  }
}

export const applyXHR = (xhr?: XMLHttpRequest): Observer<XHRNext> => {
  return (next, tearDown) => {
    if (!xhr) return
    const tearDownGroup: (() => void)[] = []
    tearDownGroup.push(hookMethodDangerously(xhr, 'open', hookOpen)())
    tearDownGroup.push(hookMethodDangerously(xhr, 'setRequestHeader', hookSetHeader)())
    tearDownGroup.push(hookMethodDangerously(xhr, 'send', hookSend)(next))
    tearDown(() => {
      tearDownGroup.forEach((e) => e())
    })
  }
}

export const FETCH_SUBJECT_NAME = 'fetch_0'

export type FetchNext = ([req, options]: [req: Request | string, options: RequestInit]) => (res?: Response) => void

export const hookFetch =
  (
    _fetch: typeof window.fetch,
    next: (args: ParametersForFn<FetchNext>) => ReturnType<NextLink<FetchNext>>,
  ): typeof window.fetch =>
  (req, options = {}) => {
    const endNext = next([req, options])
    const fetchPromise = _fetch(req, options)

    fetchPromise.then(
      (res: Response) => {
        // @ts-expect-error
        endNext(res)
      },
      () => {
        // @ts-expect-error
        endNext(undefined)
      },
    )

    return fetchPromise
  }

export const observeFetch: Observer<FetchNext> = (next, tearDown) => {
  const global = getDefaultBrowser()
  if (!global || !fetch) return

  const tearDownGroup: (() => void)[] = []
  tearDownGroup.push(hookMethodDangerously(global, 'fetch', hookFetch)(next))
  tearDown(() => {
    tearDownGroup.forEach((e) => e())
  })
}

export const fetchSubject: [string, Observer<FetchNext>] = [FETCH_SUBJECT_NAME, observeFetch]
