import { isString, SafeObservable, noop } from '@slardar/sdk-template'

import { XHRNext, ResourceNext } from '../../collector'
import { HttpPayload, HttpReport, ReportToReportEvent } from '../../types'
import { getFullUrl, now } from '../../utils'

import { AjaxMonitorConfig, NewXMLHttpRequest } from './type'
import { isSensitiveHeader, checkIsIgnored } from './util'

export type HTTPGetterWithXhrObserver = (
  report: (ev: ReportToReportEvent<HttpReport>) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<XHRNext>, () => SafeObservable<ResourceNext>],
  config: AjaxMonitorConfig,
) => void

export const httpGetterWithXhrObserver: HTTPGetterWithXhrObserver = (
  report,
  tearDownGroup,
  [xhrObserver, getResourceObserver],
  xhrConfig,
) => {
  const { setTraceHeader, ignoreUrls, hookCbAtReq } = xhrConfig
  tearDownGroup.push(
    xhrObserver[0](([_method, _url, _start, xhr]) => {
      if (checkIsIgnored(ignoreUrls, _url)) return noop

      setTraceHeader && setTraceHeader(_url, (key: string, value: string) => xhr.setRequestHeader(key, value))
      const reportWithContext = hookCbAtReq(report)

      const fullUrl = getFullUrl(_url)
      let timing: PerformanceResourceTiming | undefined = undefined
      const resourceTearDown = getResourceObserver()[0]((entry) => {
        fullUrl === entry.name && !timing && (timing = entry)
      })

      return (xhrAfterReady) => {
        const payload = getEventParams(xhrAfterReady, xhrConfig)
        setTimeout(() => {
          timing && (payload.response.timing = timing)
          reportWithContext && reportWithContext({ ev_type: 'http', payload })
          resourceTearDown()
        }, 100)
      }
    }),
  )
}

function formatXHRAllResponseHeaders(headers: string): Record<string, string> {
  if (isString(headers) && headers) {
    return headers.split('\r\n').reduce((result, line) => {
      if (isString(line)) {
        const [name, value] = line.split(': ')
        !isSensitiveHeader(name, value) && (result[name.toLowerCase()] = value)
      }
      return result
    }, {} as Record<string, string>)
  }
  return {}
}

function formatRequestHeaders(headers: Record<string, string>): Record<string, string> {
  return Object.keys(headers).reduce((result, name) => {
    !isSensitiveHeader(name, headers[name]) && (result[name.toLowerCase()] = headers[name])
    return result
  }, {} as Record<string, string>)
}

export const getEventParams = (xhr: NewXMLHttpRequest, props: AjaxMonitorConfig): HttpPayload => {
  const { _method, _reqHeaders, _url, _start, _data } = xhr
  const fullUrl = getFullUrl(_url)
  const params: HttpPayload = {
    api: 'xhr',
    request: {
      url: fullUrl,
      method: (_method || '').toLowerCase(),
      headers: _reqHeaders && formatRequestHeaders(_reqHeaders),
      timestamp: _start,
    },
    response: {
      status: xhr.status || 0,
      is_custom_error: false,
      timestamp: now(),
    },
    duration: now() - _start,
  }

  if (typeof xhr.getAllResponseHeaders === 'function') {
    params.response.headers = formatXHRAllResponseHeaders(xhr.getAllResponseHeaders())
  }

  const status = params.response.status

  const { collectBodyOnError, extraExtractor } = props
  try {
    const extra = extraExtractor?.(xhr.response, params)
    extra && (params.extra = extra)
    extra && (params.response.is_custom_error = true)
    // 非 2xx , 3xx 请求，上传request body
    if (collectBodyOnError && status >= 400) {
      params.request.body = _data ? `${_data}` : undefined
      params.response.body = xhr.response ? `${xhr.response}` : undefined
    }
  } catch (_o) {
    // do nothing
  }
  return params
}
