import { SafeObservable, noop, isString } from '@slardar/sdk-template'

import { FetchNext, ResourceNext } from '../../collector'
import { HttpReport, ReportToReportEvent, HttpPayload } from '../../types'
import { now, getFullUrl } from '../../utils'

import { FetchMonitorConfig } from './type'
import { checkIsIgnored, isSensitiveHeader, applySendOnce } from './util'

export type HTTPGetterWithFetchObserver = (
  report: (ev: ReportToReportEvent<HttpReport>) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<FetchNext>, () => SafeObservable<ResourceNext>],
  config: FetchMonitorConfig,
) => void

export const httpGetterWithFetchObserver: HTTPGetterWithFetchObserver = (
  report,
  tearDownGroup,
  [fetchObserver, getResourceObserver],
  fetchConfig,
) => {
  const { setTraceHeader, ignoreUrls, hookCbAtReq } = fetchConfig
  // eslint-disable-next-line compat/compat
  const Headers = window.Headers
  // eslint-disable-next-line compat/compat
  const Request = window.Request
  if (!Request || !Headers) return

  tearDownGroup.push(
    fetchObserver[0](([req, options]) => {
      const url = getFullUrl(req instanceof Request ? req.url : req)
      if (!isHttpURL(url) || checkIsIgnored(ignoreUrls, url)) {
        return noop
      }

      setTraceHeader &&
        setTraceHeader(url, (key: string, value: string) => addHeader(key, value, req, options, Request, Headers))

      const reportWithContext = hookCbAtReq(report)

      const _start = now()

      let timing: PerformanceResourceTiming | undefined = undefined

      const resourceTearDown = getResourceObserver()[0]((entry) => {
        url === entry.name && !timing && (timing = entry)
      })

      return (res: Response | undefined) => {
        const payload = getEventParams(req, options, res, Request, Headers, fetchConfig, _start)

        const reportFetchData = applySendOnce((data: HttpPayload) => {
          timing && (data.response.timing = timing)
          reportWithContext && reportWithContext({ ev_type: 'http', payload: data })
          resourceTearDown()
        })

        setTimeout(() => {
          reportFetchData(payload)
        }, 1000)
      }
    }),
  )
}

const isHttpURL = (url?: string): boolean => {
  if (!isString(url)) {
    return false
  }
  const [protocol, path] = url.split(':')
  return !path || protocol === 'http' || protocol === 'https'
}

const isRequest = (req: Request | string, Request: typeof window.Request): req is Request => {
  return req instanceof Request
}

const addHeader = (
  key: string,
  value: string,
  req: Request | string,
  options: RequestInit,
  Request: typeof window.Request,
  Headers: typeof window.Headers,
) => {
  if (isRequest(req, Request)) {
    req.headers.set(key, value)
  } else if (options.headers instanceof Headers) {
    options.headers.set(key, value)
  } else {
    options.headers = {
      ...options.headers,
      [key]: value,
    }
  }
}

export const getFetchMethod = (req: Request | string, options: RequestInit, Request: typeof window.Request) => {
  let method = (options && options.method) || 'get'
  if (isRequest(req, Request)) {
    method = req.method || method
  }
  return method.toLowerCase()
}

const mergeHeaders = (
  Headers: typeof window.Headers,
  ...headers: (HeadersInit | undefined)[]
): Record<string, string> => {
  try {
    return headers.reduce((sum: Record<string, string>, cur) => {
      // eslint-disable-next-line compat/compat
      new Headers(cur || {}).forEach((val, key) => !isSensitiveHeader(key, val) && (sum[key] = val))
      return sum
    }, {})
  } catch {
    return {}
  }
}

const getFetchBody = (req: Request | string, options: RequestInit, Request: typeof window.Request) => {
  if (isRequest(req, Request)) {
    return req.body
  } else {
    return options?.body
  }
}

const getEventParams = (
  req: Request | string,
  options: RequestInit,
  res: Response | undefined,
  Request: typeof window.Request,
  Headers: typeof window.Headers,
  props: FetchMonitorConfig,
  _start: number,
): HttpPayload => {
  const params: HttpPayload = {
    api: 'fetch',
    request: {
      method: getFetchMethod(req, options, Request),
      timestamp: _start,
      url: getFullUrl(req instanceof Request ? req.url : req),
      headers: mergeHeaders(Headers, (req as Request).headers, options.headers),
    },
    response: {
      status: (res && res.status) || 0,
      is_custom_error: false,
      timestamp: now(),
    },
    duration: now() - _start,
  }
  const { collectBodyOnError, extraExtractor } = props
  if (res) {
    try {
      params.response.headers = mergeHeaders(Headers, res.headers)
      try {
        extraExtractor &&
          res
            .clone()
            .json()
            .then((o: any) => {
              const extra = extraExtractor(o, params)
              if (extra) {
                params.extra = extra
                params.response.is_custom_error = true
              }
            })
            .catch(noop)
      } catch (_o) {
        // do nothing
      }

      if (collectBodyOnError && res.status >= 400) {
        params.request.body = getFetchBody(req, options, Request)?.toString()
      }
    } catch {
      // do nothing
    }
  } else {
    collectBodyOnError && (params.request.body = getFetchBody(req, options, Request)?.toString())
  }
  return params
}
