import { Observer } from '@slardar/sdk-template'

import { getDefaultPerformanceObserver } from '../utils'

export const PERFORMANCE_EVENT_TYPE = ['first-input', 'paint', 'navigation'] // used by paintMonitor / fidMonitor / timingMonitor

export const RESOURCE_TYPE = ['resource'] // used by resourceMonitor / resourceErrorMonitor / HttpMonitor

export const LONGTASK_TYPE = ['longtask'] // used by ttiMonitor / longtaskMonitor / actionMonitor / mpfidMonitor

const applyPerformanceObserver = <C extends PerformanceEntry = PerformanceEntry>(
  PerformanceObserver: typeof window.PerformanceObserver | undefined | null | false,
  callback: (entry: C, index: number, array: C[], ob: PerformanceObserver) => void,
  onFail?: () => void,
) => {
  const observer =
    PerformanceObserver &&
    new PerformanceObserver((list, ob) => {
      if (list.getEntries) {
        list.getEntries().forEach((val, i, arr) => callback(val as C, i, arr as C[], ob))
      } else {
        onFail && onFail()
      }
    })

  const observe = (types: string[]) => {
    if (!PerformanceObserver || !observer) return onFail && onFail()
    try {
      observer.observe({ entryTypes: types })
    } catch {
      return onFail && onFail()
    }
  }

  const bufferedObserver = (type: string) => {
    if (!PerformanceObserver || !observer) return onFail && onFail()
    try {
      observer.observe({ type, buffered: true })
    } catch {
      return onFail && onFail()
    }
    observer.observe({ type, buffered: false })
  }

  const disconnect = () => observer && observer.disconnect()

  return [observe, bufferedObserver, disconnect] as const
}

export const observePerf = <C extends PerformanceEntry = PerformanceEntry>(
  Ob: typeof window.PerformanceObserver,
  pipe: (entry: C) => void,
  types: string[],
) => {
  const [observe, , disconnect] = applyPerformanceObserver<C>(Ob, pipe)
  observe(types)
  return disconnect
}

export const observePerfWithBuffer = <C extends PerformanceEntry = PerformanceEntry>(
  Ob: typeof window.PerformanceObserver,
  pipe: (entry: C) => void,
  type: string,
) => {
  const [, bufferedObserver, disconnect] = applyPerformanceObserver<C>(Ob, pipe)
  bufferedObserver(type)
  return disconnect
}

export const LONGTASK_SUBJECT_NAME = 'longtask_0'

export type LongtaskNext = (args: PerformanceEntry) => void

export const observeLongtask: Observer<LongtaskNext> = (next, tearDown) => {
  const PerformanceObserver = getDefaultPerformanceObserver()
  if (!PerformanceObserver) return
  tearDown(observePerf(PerformanceObserver, next, LONGTASK_TYPE))
}

export const longtaskSubject: [string, Observer<LongtaskNext>] = [LONGTASK_SUBJECT_NAME, observeLongtask]

export const RESOURCE_SUBJECT_NAME = 'resource_0'

export type ResourceNext = (args: PerformanceResourceTiming) => void

export const observeResource: Observer<ResourceNext> = (next, tearDown) => {
  const PerformanceObserver = getDefaultPerformanceObserver()
  if (!PerformanceObserver) return
  tearDown(observePerf(PerformanceObserver, next, RESOURCE_TYPE))
}

export const resourceSubject: [string, Observer<ResourceNext>] = [RESOURCE_SUBJECT_NAME, observeResource]
