/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { isObject } from '@slardar/sdk-template'

import { CallBack } from '../types'

import { getDefaultBrowser, getDefaultDocument } from './defaults'

export function onPageLoad(callback: () => void) {
  const window = getDefaultBrowser()
  const document = getDefaultDocument()
  if (!window || !document) return

  if (document.readyState === 'complete') {
    callback()
    return
  }
  window.addEventListener(
    'load',
    () => {
      setTimeout(() => {
        callback()
      }, 0)
    },
    false,
  )
}

export function onDOMContentLoaded(callback: () => void) {
  const window = getDefaultBrowser()
  const document = getDefaultDocument()
  if (!window || !document) return

  if (document.readyState === 'loading') {
    window.addEventListener(
      'DOMContentLoaded',
      () => {
        setTimeout(() => {
          callback()
        }, 0)
      },
      false,
    )
    return
  }
  callback()
}
export interface OnPageUnloadCallback {
  (event: Event): void
}
export interface PageHiddenCallback {
  (): void
}

export const invokeCallbackOnce = <T>(cb?: CallBack<T>): [(params: T) => void] => {
  let hasInvoked = false
  const invoke = (params: T) => {
    if (hasInvoked) return
    hasInvoked = true
    cb && cb(params)
  }
  return [invoke]
}

export const onPageUnload = (cb: OnPageUnloadCallback) => {
  const window = getDefaultBrowser()
  if (!window) return

  const [invokeCbOnce] = invokeCallbackOnce<Event>(cb)

  ;['unload', 'beforeunload', 'pagehide'].forEach((ev) => {
    window.addEventListener(ev, invokeCbOnce)
  })
}

export const onceHidden = (cb: PageHiddenCallback, once = true) => {
  const document = getDefaultDocument()
  const window = getDefaultBrowser()

  if (!document || !window) return

  if (document.visibilityState === 'hidden') {
    cb()
    return
  }
  const onVisibilityChange = () => {
    if (document.visibilityState === 'hidden') {
      cb()
      once && removeEventListener('visibilitychange', onVisibilityChange, true)
    }
  }
  window.addEventListener('visibilitychange', onVisibilityChange, true)
}

export const loadScript = (url: string, callback: () => void) => {
  /* istanbul ignore next */
  const document = getDefaultDocument()
  // untestable for now
  /* istanbul ignore next */
  if (document) {
    const script = document.createElement('script')

    script.src = url
    script.crossOrigin = 'anonymous'
    script.onload = callback

    document.head?.appendChild(script)
  }
}

export const getConfig = <T>(c: Partial<T> | boolean | undefined | null, defaultConfig: T): T | false => {
  if (isObject(c)) {
    return { ...defaultConfig, ...c }
  } else {
    return c ? defaultConfig : false
  }
}
