import { getDefaultDocument } from '../../utils'

import { BreadcrumbMonitorConstructor } from './type'
import { applyBreadcrumb, applyDomAndKeyPress, domBreadcrumb, triggerHandlers } from './util'

export { BreadcrumbProps, GetBreadcrumbs, AddBreadcrumb, PreBreadcrumb } from './type'

export const BreadcrumbMonitor: BreadcrumbMonitorConstructor = (document = getDefaultDocument()) => {
  if (!document) {
    return
  }

  return (props, _cb) => {
    const { maxBreadcrumbs, onAddBreadcrumb, onMaxBreadcrumbs, dom } = props
    const [domEventHandler, keypressHandler] = applyDomAndKeyPress(100)
    const [getBreadcrumbs, addBreadcrumb] = applyBreadcrumb(maxBreadcrumbs, onAddBreadcrumb, onMaxBreadcrumbs)
    const createDomBreadcrumb = domBreadcrumb(addBreadcrumb)

    const handlers: any[] = []
    if (dom) {
      handlers.push(domEventHandler('click', triggerHandlers(createDomBreadcrumb, 'dom')))
      handlers.push(keypressHandler(triggerHandlers(createDomBreadcrumb, 'dom')))
      document.addEventListener('click', handlers[0])
      document.addEventListener('keypress', handlers[1])
    }
    const teardown = () => {
      document.removeEventListener('click', handlers[0])
      document.removeEventListener('keypress', handlers[1])
    }
    return [getBreadcrumbs, addBreadcrumb, teardown]
  }
}
