import { JsError } from '../../types'
import { reportSelfError } from '../../utils'

const isSameErrorString = (current: string | undefined, previous: string | undefined) =>
  current && previous && current === previous

const shouldDropEvent = (currentError: JsError | undefined, previousError: JsError | undefined): boolean => {
  if (!currentError || !previousError) {
    return false
  }

  if (
    isSameErrorString(currentError.message, previousError.message) &&
    isSameErrorString(currentError.stack, previousError.stack)
  ) {
    return true
  }
  return false
}

export const dedupe = () => {
  let __previousError: JsError | undefined
  return (currentError: JsError | undefined): JsError | undefined => {
    // just in case something goes wrong
    try {
      if (shouldDropEvent(currentError, __previousError)) {
        __previousError = currentError
        return undefined
      }
    } catch (e) {
      reportSelfError(e)
    }
    __previousError = currentError
    return currentError
  }
}
