import { EventSender, SdkTransport } from './types'

export type BatchEvent<E> = {
  ev_type: 'batch'
  list: E[]
}

export type FailureCallbackType = (err: Error, data: string) => void

export type BatchSender<E> = EventSender<E> & {
  fail?: (callback: FailureCallbackType) => void
  getSize: () => number
  getWait: () => number
  setSize: (v: number) => void
  setWait: (v: number) => void
  getEndpoint: () => string
  setEndpoint: (v: string) => void
  flush: () => void
  getBatchData: () => string
  clear: () => void
}
export interface BatchSenderConfig {
  endpoint: string
  transport: SdkTransport
  size?: number
  wait?: number
}

const DEFAULT_SIZE = 10
const DEFAULT_WAIT = 1000

export const stringifyBatch = (list: any[]): string =>
  JSON.stringify({
    ev_type: 'batch',
    list,
  })

export function createBatchSender<E>(config: BatchSenderConfig): BatchSender<E> {
  const { transport } = config
  let { endpoint, size = DEFAULT_SIZE, wait = DEFAULT_WAIT } = config
  let batch = [] as E[]
  let tid = 0
  let fail: FailureCallbackType | undefined
  const sender = {
    getSize() {
      return size
    },
    getWait() {
      return wait
    },
    setSize(v: number) {
      size = v
    },
    setWait(v: number) {
      wait = v
    },
    getEndpoint() {
      return endpoint
    },
    setEndpoint(v: string) {
      endpoint = v
    },
    send(e: E) {
      batch.push(e)
      if (batch.length >= size) {
        sendBatch.call(this)
      }
      clearTimeout(tid)
      tid = (setTimeout as Window['setTimeout'])(sendBatch.bind(this), wait)
    },
    flush() {
      clearTimeout(tid)
      sendBatch.call(this)
    },
    getBatchData() {
      return batch.length ? stringifyBatch(batch) : ''
    },
    clear() {
      clearTimeout(tid)
      batch = []
    },
    fail(cb: FailureCallbackType) {
      fail = cb
    },
  }

  function sendBatch(this: BatchSender<E>) {
    if (!batch.length) {
      return
    }
    const data = this.getBatchData()
    transport.post({
      url: endpoint,
      data,
      fail(err: Error) {
        fail && fail(err, data)
      },
    })
    batch = []
  }

  return sender
}
