import { SafeObservable } from '@slardar/sdk-template'

import { RouteNext } from '../../collector/route'
import { WebReportEvent } from '../../types'

import { PageviewProps, PageviewSourceType } from './type'
import { getDefaultExtractor, applyOnPidChange, applyReportPageview, applyOnUrlChange, isManualMode } from './util'

type PVGetterWithRouteContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: SafeObservable<RouteNext>[] | [],
  config: PageviewProps,
) => [(pid: string) => void]

export const pvGetterWithRouteObserver: PVGetterWithRouteContext = (report, tearDownGroup, routeObservers, config) => {
  const { sendInit: sendInitFlag, initPid, routeMode, extractPid, onPidUpdate } = config

  const extractPidFromUrl = isManualMode(routeMode) ? () => '' : getDefaultExtractor(routeMode)
  const customPidFromUrl = extractPid || (() => undefined)
  const getFinalPid = (url: string): string => customPidFromUrl(url) ?? extractPidFromUrl(url)

  const [onPidChange, sendInit] = applyOnPidChange(
    applyReportPageview(report),
    initPid || getFinalPid(location.href),
    extractPidFromUrl(location.href),
    onPidUpdate,
  )

  if (!isManualMode(routeMode)) {
    const [onUrlChange] = applyOnUrlChange(
      (sources: PageviewSourceType, url: string) => onPidChange(sources, extractPidFromUrl(url), customPidFromUrl(url)),
      '',
    )
    routeObservers.length &&
      routeObservers.forEach((routeObserver) =>
        tearDownGroup.push(routeObserver[0]((value: string) => onUrlChange(routeMode as PageviewSourceType, value))),
      )
  }

  if (sendInitFlag) {
    sendInit()
  }
  return [onPidChange.bind(null, PageviewSourceType.user_set)]
}
