import { isHitBySampleRate } from '@slardar/sdk-template'

import { getConfig } from '../../utils'

import { TraceConfig } from './type'

const VERSION = '03'

const SAMPLED = '01'

const TRACEPARENT = 'traceparent'

const DEFAULT_TRACE_CONFIG: TraceConfig = {
  sampleRate: 1,
  origins: [],
}

interface MsCryptoWindow extends Window {
  msCrypto?: Crypto
}

export const uuid4 = (): string => {
  const crypto = window.crypto || (window as MsCryptoWindow).msCrypto

  if (crypto !== void 0 && crypto.getRandomValues) {
    const arr = new Uint16Array(8)
    crypto.getRandomValues(arr)
    const pad = (num: number): string => {
      let v = num.toString(16)
      while (v.length < 4) {
        v = `0${v}`
      }
      return v
    }

    return pad(arr[0]) + pad(arr[1]) + pad(arr[2]) + pad(arr[3]) + pad(arr[4]) + pad(arr[5]) + pad(arr[6]) + pad(arr[7])
  }

  return 'xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx'.replace(/[x]/g, () => {
    const r = (Math.random() * 16) | 0
    return r.toString(16)
  })
}

export const setTraceContext = (traceConfig: Partial<TraceConfig> | boolean | undefined) => {
  const config = getConfig(traceConfig, DEFAULT_TRACE_CONFIG)
  if (!config) return

  const sampled = isHitBySampleRate(config.sampleRate!)
  if (!sampled) return

  return (url: string, cb: (key: string, value: string) => void) => {
    const { origins } = config
    if (origins.length && Boolean(url.match(new RegExp(origins.join('|'))))) {
      cb(TRACEPARENT, `${VERSION}-${uuid4()}-${uuid4().substring(16)}-${SAMPLED}`)
    }
  }
}

export const setVolTraceContext = (traceConfig: Partial<TraceConfig> | boolean | undefined, tracestate: string) => {
  const config = getConfig(traceConfig, DEFAULT_TRACE_CONFIG)
  if (!config) return

  const sampled = isHitBySampleRate(config.sampleRate!)
  if (!sampled) return

  return (url: string, cb: (key: string, value: string) => void) => {
    const { origins } = config
    if (origins.length && Boolean(url.match(new RegExp(origins.join('|'))))) {
      cb('x-rum-traceparent', `00-${uuid4()}-${uuid4().substring(16)}-${SAMPLED}`)
      cb('x-rum-tracestate', tracestate)
    }
  }
}
