import { WebReport, WebReportEvent } from '../../types'
import { getDefaultPromise, applyRequestIdleCallback, now } from '../../utils'

import { BlankRelatedErrorInfo } from './type'

export const DEFAULT_IGNORE_TAGS = ['SCRIPT', 'STYLE', 'META', 'HEAD']
export const ERROR_RELATED_TYPE = ['js_error', 'http', 'resource_error']
export const DEFAULT_THRESHOLD = 1.5

export const MAX_RENDER_TIME = 10000
export const MAX_CHECK_TIME = 8000
export const DEFAULT_CHECK_TIME = 2000
export const MAX_RELATED_TIME = 10000
export const DEFAULT_T0_DATA_URL_QUALITY = 0.1

const CHECK_EXCLUSIVE_EV_TYPES: WebReport['ev_type'][] = [
  'performance',
  'performance_longtask',
  'performance_timing',
  'custom',
]

export const isSkipBeforeCheck = (ev: WebReportEvent) => ~CHECK_EXCLUSIVE_EV_TYPES.indexOf(ev.ev_type)

export const blankErrorCompare = (
  relatedError: BlankRelatedErrorInfo | undefined,
  ev: WebReportEvent,
): BlankRelatedErrorInfo | undefined => {
  if (ERROR_RELATED_TYPE.indexOf(ev.ev_type) === -1) return relatedError
  if (ev.ev_type === 'http' && ev.payload.response.status < 400) {
    return relatedError
  }
  if (relatedError && ERROR_RELATED_TYPE.indexOf(relatedError.type) < ERROR_RELATED_TYPE.indexOf(ev.ev_type)) {
    return relatedError
  }
  let message = ''

  switch (ev.ev_type) {
    case 'js_error':
      message = ev.payload.error.message
      break
    case 'http':
      message = ev.payload.request.url
      break
    case 'resource_error':
      message = ev.payload.url
      break
    default:
      break
  }
  return { type: ev.ev_type, message, timestamp: now() }
}

export const measure = (
  element: Element | null | undefined | false,
  depth = 0,
  sum = 0,
  threshold = DEFAULT_THRESHOLD,
  ignoreTags = DEFAULT_IGNORE_TAGS,
): number => {
  if (!element || ignoreTags.indexOf(element.tagName) > -1 || sum >= threshold || depth > 4) {
    return sum
  }

  const score = (() => {
    if (!depth) return 0
    const { top, height } = element.getBoundingClientRect()
    return top > innerHeight || height <= 0 ? 0 : 1 / Math.pow(2, depth - 1)
  })()

  return [].reduceRight.call(
    element.children,
    (all, child) => measure(child, depth + 1, all as number, threshold, ignoreTags),
    sum + score,
  ) as number
}

declare global {
  interface Window {
    html2canvas?: (element: HTMLElement, options?: any) => Promise<HTMLCanvasElement>
  }
}

interface TakeScreenshotType {
  cb: (base64: string) => void
  screenshotUrl: string
  window: Window
  document: Document
  mask: boolean
  partialShot: boolean
  quality: number
  rootSelector?: string
}

export const screenshot = ({
  cb,
  screenshotUrl,
  window,
  document,
  mask,
  partialShot,
  quality,
  rootSelector,
}: TakeScreenshotType) => {
  if (!getDefaultPromise() || !window || !document) {
    return
  }

  function onload() {
    const requestIdleCallback = applyRequestIdleCallback(window)
    requestIdleCallback(() => {
      window.html2canvas &&
        window.html2canvas!((partialShot && rootSelector && document.querySelector(rootSelector)) || document!.body, {
          scale: 360 / window!.innerWidth,
          mask,
        }).then((canvas) => {
          cb(canvas.toDataURL('image/jpeg', quality))
        })
    })
  }

  if (window.html2canvas) {
    return onload()
  }
  const script = document.createElement('script')
  script.src = screenshotUrl
  // eslint-disable-next-line compat/compat
  document.head?.appendChild(script)
  script.onload = onload
}
