/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { isFunction, isString } from '@slardar/sdk-template'

import { ResourceErrorPayload } from '../../types'
import { getFullUrl } from '../../utils'

import { ResourceErrorData } from './type'

export type Target = HTMLScriptElement | HTMLLinkElement | HTMLImageElement | HTMLVideoElement

export function isHTMLLinkElement(target: Target): target is HTMLLinkElement {
  return target.tagName.toLowerCase() === 'link'
}

export function getElementAttr<T extends Target>(target: T, attr: KeysMatching<T, string, string>) {
  if (isFunction(target.getAttribute)) {
    // returns what was in the HTML. It may be a relative URL.
    return target.getAttribute(attr) || ''
  }

  // target.src returns absoulute path
  return target[attr] || ''
}

// 从 T 中获取满足以下条件的属性：属性的类型是 V, 属性的值是 M
type KeysMatching<T, V, M> = { [K in keyof T]-?: K extends V ? (T[K] extends M ? K : never) : never }[keyof T]

export const getSrc = (target: Target): string =>
  getElementAttr(target, isHTMLLinkElement(target) ? 'href' : ('src' as any))

export const getDataFromEvent = (e: Event): ResourceErrorData | undefined => {
  const target = (e.target || e.srcElement) as Target | null
  if (!target) {
    return
  }
  const tagName = target.tagName
  if (!tagName || !isString(tagName)) {
    return
  }
  const src = getSrc(target)
  return {
    url: src,
    tagName: tagName,
  }
}

export const buildPayload = (
  data: ResourceErrorData,
  getEntriesByName: (name: string) => PerformanceEntryList,
): ResourceErrorPayload => {
  const { url, tagName, xpath } = data
  const fullUrl = getFullUrl(url)
  const timing = getEntriesByName(fullUrl)[0] as PerformanceResourceTiming | undefined

  return {
    type: tagName.toLowerCase(),
    url: fullUrl,
    xpath,
    timing,
  }
}
