import { isArray, isString, isObject } from './is'

export type IgnoreRegExp = RegExp | null
export type IgnoreList = Array<string | RegExp>

export const camelToKebab = (str: string) => {
  return str.replace(/([a-z])([A-Z])/g, (_, a, b) => `${a}-${b.toLowerCase()}`)
}

export function getRegexp(ignore: IgnoreList) {
  if (!isArray(ignore)) {
    return null
  }

  return ignore.length ? joinRegExp(ignore) : null
}

export function joinRegExp(patterns: IgnoreList) {
  const sources: string[] = []
  const len = patterns.length

  for (let i = 0; i < len; i++) {
    const pattern = patterns[i]
    if (isString(pattern)) {
      sources.push((pattern as string).replace(/([.*+?^=!:${}()|[\]/\\])/g, '\\$1'))
    } else if (pattern && (pattern as RegExp).source) {
      sources.push((pattern as RegExp).source)
    }
  }
  return new RegExp(sources.join('|'), 'i')
}

export function safeStringify(a: unknown): string {
  try {
    return isString(a) ? a : JSON.stringify(a)
  } catch (err) {
    return '[FAILED_TO_STRINGIFY]:' + String(err)
  }
}

export function safeJoin(input: any[], delimiter?: string): string {
  if (!isArray(input)) {
    return ''
  }

  const output = []
  // eslint-disable-next-line @typescript-eslint/prefer-for-of
  for (let i = 0; i < input.length; i++) {
    try {
      output.push(String(input[i]))
    } catch (e) {
      output.push('[value cannot be serialized]')
    }
  }
  return output.join(delimiter)
}
/**
 * get query string from map
 * @param  {Record<string | number, string | number>} map
 */
export const joinQueryWithMap = (map: Record<string | number, string | number | undefined>) => {
  return isObject(map)
    ? Object.keys(map)
        .reduce((result, currentKey) => {
          result += `&${currentKey}=${map[currentKey]}`
          return result
        }, '')
        .replace('&', '?')
    : ''
}
