import { SafeObservable } from '@slardar/sdk-template'

import { LoadNext, UnloadNext } from '../../collector/page'
import { LongtaskNext } from '../../collector/pref'
import { getDefaultPerformanceObserver, getDefaultPerformance, applyPerformance } from '../../utils'
import { MetricEnum } from '../performance/type'
import { initMetric } from '../performance/util'

import { FCP_ENTRY_NAME } from './paint'
import { PerfMetricGetterWithLoadContext } from './type'
import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

export const MPFID_METRIC_NAME = MetricEnum.mpfid

const getMPFID: PerfMetricGetterWithLoadContext = (
  report,
  tearDownGroup,
  [loadObserver, , longtaskObserver]: [
    SafeObservable<LoadNext>,
    SafeObservable<UnloadNext>,
    SafeObservable<LongtaskNext>,
  ],
) => {
  const PerformanceObserver = getDefaultPerformanceObserver()
  const performance = getDefaultPerformance()

  const metric = initMetric(MPFID_METRIC_NAME, 0)
  const list: PerformanceEntry[] = []
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)

  if (!PerformanceObserver) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  tearDownGroup.push(
    longtaskObserver[0]((entry: PerformanceEntry) => {
      list.push(entry)
    }),
  )

  const calculateAndSendMetric = () => {
    const [, , , , getEntriesByName] = applyPerformance(performance)
    const paint = getEntriesByName(FCP_ENTRY_NAME)[0]
    const FCPStartTime = (paint && paint.startTime) || 0
    metric.value = list.reduce(
      (res, { duration, startTime }) => (res < duration && startTime > FCPStartTime ? duration : res),
      0,
    )
    list.length = 0
    sendOnceAndTearDown(metric)
  }

  tearDownGroup.push(
    loadObserver[0](() => {
      setTimeout(calculateAndSendMetric, 200)
    }),
  )
}

export const MPFID: [MetricEnum, PerfMetricGetterWithLoadContext] = [MPFID_METRIC_NAME, getMPFID]
