/* eslint-disable @typescript-eslint/prefer-optional-chain */
import { PerformanceReport } from '../../types/report'
import { PerformancePayload } from '../../types/thrift/browser/collector_v2/performance'

export const defaultMetricContext = {
  isSupport: true,
  isPolyfill: false,
  isBounced: false,
  isCustom: false,
  type: 'perf',
}

export const initMetric = (name: string, value: number): PerformancePayload => {
  return {
    name,
    value,
    ...defaultMetricContext,
  }
}

type GetFunction<P, B> = (params?: P) => B

export const invokeGetOnce = <T, B>(cb: GetFunction<T, B>): [(params?: T) => B | undefined] => {
  let hasGetted = false
  const invoke = (params?: T) => {
    if (hasGetted) return undefined

    // 如果执行后依旧未拿到，直接返回
    const value = cb && cb(params)
    if (!value) return undefined

    // 标记【拿到过数据】并返回数据
    hasGetted = true
    return value
  }
  return [invoke]
}

export const buildSingleMetricPayload = (singleMetric: PerformancePayload): PerformanceReport => ({
  ev_type: 'performance',
  payload: singleMetric,
})
