import { SafeObservable, getRegexp } from '@slardar/sdk-template'

import { ErrorNext } from '../../collector/error'
import { WebReportEvent } from '../../types'
import { getDefaultPerformance, applyPerformance, getDefaultBrowser } from '../../utils'

import { ResourceErrorData, ResourceErrorProps } from './type'
import { getDataFromEvent, buildPayload } from './util'

type ResourceErrorGetterWithContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<ErrorNext>],
  config: ResourceErrorProps,
) => ((data: ResourceErrorData) => void) | undefined

const RESOURCE_ERROR_EV_TYPE = 'resource_error'

export const resourceErrorGetterWithContext: ResourceErrorGetterWithContext = (
  report,
  tearDownGroup,
  [errorObserver],
  config,
) => {
  const window = getDefaultBrowser()
  if (!window) return

  const { ignoreUrls, includeUrls, dedupe } = config
  const includeRegExp = getRegexp(includeUrls)
  const ignoreRegExp = getRegexp(ignoreUrls)
  const [, , , , getEntriesByName] = applyPerformance(getDefaultPerformance())

  let lastErrPath: undefined | string = undefined

  const reportResourceError = (data: ResourceErrorData) => {
    const locationHref = location && location.href
    if (locationHref && data.url === locationHref) return
    if ((includeRegExp && !includeRegExp.test(data.url)) || (ignoreRegExp && ignoreRegExp.test(data.url))) return

    if (!data.url) return

    if (dedupe && data.url === lastErrPath) return

    lastErrPath = data.url
    // when the error occurs, the timing has been pushed into the buffer pool
    // so use getEntriesByName instead of resourceObserver
    const payload = buildPayload(data, getEntriesByName)
    if (payload) {
      report({
        ev_type: RESOURCE_ERROR_EV_TYPE,
        payload: payload,
      })
    }
  }

  tearDownGroup.push(
    errorObserver[0]((event) => {
      const e = event || window.event
      if (!e) {
        return
      }
      const data = getDataFromEvent(e)
      data && reportResourceError(data)
    }),
  )

  return reportResourceError
}
