import {
  isError,
  isEvent,
  isPlainObject,
  isErrorEvent,
  isPromiseRejectionEvent,
  isString,
  pick,
  safeStringify,
} from '@slardar/sdk-template'

import { JsError } from '../../types'

const ERROR_FIELDS = ['name', 'message', 'stack', 'filename', 'lineno', 'colno']

export const normalize = (ex: any): JsError | undefined => {
  let error: JsError | undefined
  if (!isError(ex)) {
    if (isPlainObject(ex) || isEvent(ex) || isString(ex)) {
      error = {
        message: safeStringify(ex),
      }
    }
  } else {
    error = pick(ex, ERROR_FIELDS) as JsError
  }
  return error
}

export const normalizeError = (event: ErrorEvent): any => {
  return normalize(event.error)
}

export const normalizeException = (event: PromiseRejectionEvent | CustomEvent): any => {
  // dig the object of the rejection out of known event types
  try {
    let error: any
    // PromiseRejectionEvents store the object of the rejection under 'reason'
    // see https://developer.mozilla.org/en-US/docs/Web/API/PromiseRejectionEvent
    if ('reason' in event) {
      error = event.reason
    }
    // something, somewhere, (likely a browser extension) effectively casts PromiseRejectionEvents
    // to CustomEvents, moving the `promise` and `reason` attributes of the PRE into
    // the CustomEvent's `detail` attribute, since they're not part of CustomEvent's spec
    // see https://developer.mozilla.org/en-US/docs/Web/API/CustomEvent and
    // https://github.com/getsentry/sentry-javascript/issues/2380
    else if ('detail' in event && 'reason' in event.detail) {
      error = event.detail.reason
    }

    if (error) {
      const rejectionError = normalize(error)
      // eslint-disable-next-line @typescript-eslint/prefer-optional-chain
      return { ...rejectionError, name: (rejectionError && rejectionError.name) ?? 'UnhandledRejection' }
    }
  } catch (_oO) {
    // no-empty
  }
}

export const normalizeUnknownError = (exception: any) => {
  if (isErrorEvent(exception)) {
    return normalizeError(exception)
  } else if (isPromiseRejectionEvent(exception)) {
    return normalizeException(exception)
  } else {
    return normalize(exception)
  }
}
