import { SafeObservable } from '@slardar/sdk-template'

import { LoadNext, UnloadNext } from '../../collector/page'
import { LongtaskNext } from '../../collector/pref'
import { PerformanceLongtask } from '../../types/thrift/browser/collector_v2/performance_longtask'
import { MetricEnum } from '../performance/type'

import { PerfMetricGetterWithLoadContext } from './type'
import { wrapLongtask } from './util'

export const LONGTASK_METRIC_NAME = MetricEnum.longtask

const getLongtask: PerfMetricGetterWithLoadContext = (
  report,
  tearDownGroup,
  [, , longtaskObserver]: [SafeObservable<LoadNext>, SafeObservable<UnloadNext>, SafeObservable<LongtaskNext>],
) => {
  tearDownGroup.push(
    longtaskObserver[0]((entry: PerformanceLongtask) => {
      report(wrapLongtask(entry))
    }),
  )
}

export const LONGTASK: [MetricEnum, PerfMetricGetterWithLoadContext] = [LONGTASK_METRIC_NAME, getLongtask]
