import { WithSubject, initSubjectInGlobal } from '@slardar/sdk-template'

import { errorSubject } from '../../collector/error'
import { resourceErrorGetterWithContext } from '../../monitors/resource-error'
import { ResourceErrorProps, ReportResourceError } from '../../monitors/resource-error/type'
import { WebClient } from '../../types'

import { getPluginConfig } from './utils'

export const RESOURCE_ERROR_MONITOR_PLUGIN_NAME = 'resourceError'

export type WithReportResourceError<C extends WebClient> = C & {
  reportResourceError?: ReportResourceError
}
export type ResourceErrorMonitorConfig = ResourceErrorProps
const defaultConfig: ResourceErrorMonitorConfig = {
  includeUrls: [],
  ignoreUrls: [],
  dedupe: true,
}

export interface ResourceErrorMonitorPluginConfig {
  [RESOURCE_ERROR_MONITOR_PLUGIN_NAME]?: Partial<ResourceErrorMonitorConfig> | boolean
}

export function ResourceErrorMonitorPlugin<C extends WithSubject<WebClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, RESOURCE_ERROR_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    const reportResourceError = resourceErrorGetterWithContext(
      client.report.bind(client),
      tearDownGroup,
      [initSubjectInGlobal(client, errorSubject)],
      config,
    )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })

    reportResourceError && client.provide('reportResourceError', reportResourceError)
  })
}
