export const isHitBySampleRate = (sampleRate: number): boolean => {
  if (Math.random() < Number(sampleRate)) {
    return true
  }
  return false
}

export const isHitByRandom = (random: number, sampleRate: number): boolean => {
  if (random < Number(sampleRate)) {
    return true
  }
  return false
}
