import { SafeObservable, isFunction } from '@slardar/sdk-template'

import { LoadNext } from '../../collector/page'
import { WebReportEvent } from '../../types'
import {
  getDefaultCaf,
  getDefaultRaf,
  applyAnimationFrame,
  applyMutationObserver,
  getDefaultPerformance,
  getDefaultDocument,
  getDefaultMutationObserver,
  now,
} from '../../utils'
import { initMetric } from '../performance/util'

import { applySendOnceAndTearDown, wrapPerformanceMetric } from './util'

const FMP_METRIC_NAME = 'fmp'
const DEFAULT_IGNORE_TAGS = ['SCRIPT', 'STYLE', 'META', 'HEAD']

interface ScoreItem {
  score: number
  time: number
}

type FMPPerfMetricGetterWithLoadContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<LoadNext>],
) => void

const getScore = (element: Element | null | undefined | false, depth: number, exist: boolean, ignoreTags: string[]) => {
  if (!element || ignoreTags.indexOf(element.tagName) > -1) {
    return 0
  }

  const { children = [] } = element
  const score: number = [].slice
    .call(children)
    .reduceRight((sum, child) => sum + getScore(child, depth + 1, sum > 0, ignoreTags), 0)

  if (score <= 0 && !exist) {
    if (!isFunction(element.getBoundingClientRect)) {
      return 0
    }

    const { top, height } = element.getBoundingClientRect() || {}
    if (top > window.innerHeight || height <= 0) {
      return 0
    }
  }

  return score + 1 + 0.5 * depth
}

const getFMPInternal = ([first, ...rest]: ScoreItem[] = []) => {
  return (
    (rest &&
      rest.reduce(
        ([prev, target], cur) => {
          const diff = cur.score - prev.score
          return [cur, cur.time >= prev.time && target.rate < diff ? { time: cur.time, rate: diff } : target] as const
        },
        [first, { time: first?.time, rate: 0 as number }] as const,
      )[1].time) ||
    0
  )
}

export const getFMP: FMPPerfMetricGetterWithLoadContext = (report, tearDownGroup, [loadObserver]) => {
  const document = getDefaultDocument()
  const MutationObserver = getDefaultMutationObserver()
  const performance = getDefaultPerformance()
  const navigationStart = (performance && performance.timing && performance.timing.navigationStart) || undefined

  const metric = initMetric(FMP_METRIC_NAME, 0)
  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapPerformanceMetric, report, tearDownGroup)

  if (!document || !MutationObserver || !navigationStart) {
    metric.isSupport = false
    sendOnceAndTearDown(metric)
    return
  }

  const startTime = now()
  const list: ScoreItem[] = []
  const record = () =>
    list.push({
      time: now() - startTime,
      // eslint-disable-next-line compat/compat
      score: getScore(document && document.body, 1, false, DEFAULT_IGNORE_TAGS),
    })

  const raf = getDefaultRaf()
  const caf = getDefaultCaf()

  const [scheduleAF] = applyAnimationFrame(
    document,
    raf as Window['requestAnimationFrame'] | undefined,
    caf as Window['cancelAnimationFrame'] | undefined,
    true,
  )
  // 持续监听 dom 变化，记录下时间和得分
  // scheduleAF 可以避免强制回流，同时真正的变更是在animationFrame 中
  const [observe, disconnect] = applyMutationObserver(MutationObserver, () => scheduleAF(record))

  const tirggerFMP = (timeGap = 0) => {
    const fmp = getFMPInternal(list)
    metric.value = fmp ? fmp + timeGap : 0
    sendOnceAndTearDown(metric)
    list.length = 0
  }
  const timeGap = startTime - (navigationStart || 0)

  observe(document, { subtree: true, childList: true })

  tearDownGroup.push(disconnect)
  tearDownGroup.push(
    loadObserver[0](() => {
      setTimeout(() => {
        tirggerFMP(timeGap)
      }, 200)
    }),
  )
}
