import { Observer } from '@slardar/sdk-template'

import { getDefaultBrowser } from '../utils/defaults'

export const ERROR_SUBJECT_NAME = 'err_0'

export type ErrorNext = (args: ErrorEvent) => void

export const observeError: Observer<ErrorNext> = (next, tearDown) => {
  const window = getDefaultBrowser()
  if (!window) return

  window.addEventListener('error', next, true)

  tearDown(() => {
    window.removeEventListener('error', next, true)
  })
}

export const errorSubject: [string, Observer<ErrorNext>] = [ERROR_SUBJECT_NAME, observeError]

export const PROMISE_REJECTION_SUBJECT_NAME = 'perr_0'

export type PromiseRejectionNext = (args: PromiseRejectionEvent) => void

export const observePromiseRejection: Observer<PromiseRejectionNext> = (next, tearDown) => {
  const window = getDefaultBrowser()
  if (!window) return

  window.addEventListener('unhandledrejection', next, true)

  tearDown(() => {
    window.removeEventListener('unhandledrejection', next, true)
  })
}

export const promiseRejectionSubject: [string, Observer<PromiseRejectionNext>] = [
  PROMISE_REJECTION_SUBJECT_NAME,
  observePromiseRejection,
]
