import { AnyClient, isNumber, isObject, isString, safeStringify } from '@slardar/sdk-template'

import { CustomReport } from '../types'
import { now } from '../utils'

export type CustomEventData = {
  name: string
  metrics?: { [key: string]: number }
  categories?: { [key: string]: string }
}

export type CustomLogData = {
  content: string
  level?: string // 'debug' | 'info' | 'warn' | 'error'
  extra?: { [key: string]: number | string }
}

export type SendCustomEvent = (data: CustomEventData) => void
export type SendCustomLog = (data: CustomLogData) => void

export type WithSendCustom<C extends AnyClient> = C & {
  sendEvent?: SendCustomEvent
  sendLog?: SendCustomLog
}

const CUSTOM_EV_TYPE = 'custom'
const CUSTOM_EVENT_TYPE = 'event'
const CUSTOM_LOG_TYPE = 'log'

export const normalizeCustomEventData = (raw: unknown): CustomReport['payload'] | undefined => {
  if (!raw || !isObject(raw)) {
    return
  }

  // name is required
  if (!raw['name'] || !isString(raw['name'])) {
    return
  }

  const res = {
    name: raw['name'],
    type: CUSTOM_EVENT_TYPE,
  } as CustomReport['payload']

  if ('metrics' in raw && isObject(raw['metrics'])) {
    const rMetrics = raw['metrics']
    const metrics = {} as Record<string, number>
    for (const k in rMetrics) {
      if (isNumber(rMetrics[k])) {
        metrics[k] = rMetrics[k]
      }
    }
    res.metrics = metrics
  }

  if ('categories' in raw && isObject(raw['categories'])) {
    const rCategories = raw['categories']
    const categories = {} as Record<string, string>
    for (const k in rCategories) {
      categories[k] = safeStringify(rCategories[k])
    }
    res.categories = categories
  }

  return res
}

export const normalizeCustomLogData = (raw: unknown): CustomReport['payload'] | undefined => {
  if (!raw || !isObject(raw)) {
    return
  }

  // content is required
  if (!raw['content'] || !isString(raw['content'])) {
    return
  }

  const rContent = raw['content']

  const res = {
    content: safeStringify(rContent),
    type: CUSTOM_LOG_TYPE,
    level: 'info',
  } as CustomReport['payload']

  if ('level' in raw) {
    res.level = raw['level']
  }

  if ('extra' in raw && isObject(raw['extra'])) {
    const rExtra = raw['extra']
    const metrics = {} as Record<string, number>
    const categories = {} as Record<string, string>
    for (const k in rExtra) {
      if (isNumber(rExtra[k])) {
        metrics[k] = rExtra[k]
      } else {
        categories[k] = safeStringify(rExtra[k])
      }
    }
    res.metrics = metrics
    res.categories = categories
  }

  return res
}

export const CustomPlugin = (client: AnyClient) => {
  const sendEvent = (data: CustomEventData) => {
    const normalized = normalizeCustomEventData(data)

    if (normalized) {
      client.report({
        ev_type: CUSTOM_EV_TYPE,
        payload: normalized,
        extra: {
          timestamp: now(),
        },
      })
    }
  }

  const sendLog = (data: CustomLogData) => {
    const normalized = normalizeCustomLogData(data)

    if (normalized) {
      client.report({
        ev_type: CUSTOM_EV_TYPE,
        payload: normalized,
        extra: {
          timestamp: now(),
        },
      })
    }
  }

  client.provide('sendEvent', sendEvent)
  client.provide('sendLog', sendLog)
}
