import { noop } from '@slardar/sdk-template'

import { AddBreadcrumb, BreadcrumbMonitor, BreadcrumbProps, GetBreadcrumbs } from '../../monitors/breadcrumb'
import { WebClient } from '../../types'
import { applyMonitor } from '../../utils'

import { getPluginConfig } from './utils'

export const BREADCRUMB_MONITOR_PLUGIN_NAME = 'breadcrumb'

export type WithBreadcrumb<C extends WebClient> = C & {
  getBreadcrumbs?: GetBreadcrumbs
  addBreadcrumb?: AddBreadcrumb
}

export type BreadcrumbMonitorConfig = BreadcrumbProps

const defaultConfig: BreadcrumbMonitorConfig = {
  maxBreadcrumbs: 20,
  dom: true,
}

export interface BreadcrumbMonitorPluginConfig {
  [BREADCRUMB_MONITOR_PLUGIN_NAME]?: Partial<BreadcrumbMonitorConfig> | boolean
}

export function BreadcrumbMonitorPlugin<C extends WebClient>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, BREADCRUMB_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const [getBreadcrumbs, addBreadcrumb, tearDown] = applyMonitor(BreadcrumbMonitor, config, noop)

    client.on('report', (ev) => {
      if (ev.ev_type === 'http') {
        addBreadcrumb({
          type: 'http',
          category: ev.payload.api,
          message: '',
          data: {
            method: ev.payload.request.method,
            url: ev.payload.request.url,
            status_code: String(ev.payload.response.status),
          },
          timestamp: ev.payload.request.timestamp,
        })
      }
      return ev
    })

    client.on('beforeDestroy', tearDown)

    client.provide('getBreadcrumbs', getBreadcrumbs)
    client.provide('addBreadcrumb', addBreadcrumb)
  })
}
