import { getDefaultBrowser, getDefaultDocument, getDefaultLocation } from './defaults'

export interface ParsedUrl {
  url: string
  protocol: string
  domain: string
  query: string
  path: string
  hash: string
}

export function getFullUrl(url: string): string {
  const document = getDefaultDocument()
  if (!document || !url) return ''

  const a = document.createElement('a')
  a.href = url

  return a.href
}

export function parseUrl(url: string): ParsedUrl {
  const document = getDefaultDocument()
  if (!document || !url) {
    return {
      url,
      protocol: '',
      domain: '',
      query: '',
      path: '',
      hash: '',
    }
  }

  const a = document.createElement('a')
  a.href = url

  let path = a.pathname || '/'
  /* istanbul ignore next */
  if (path[0] !== '/') {
    path = '/' + path
  }

  return {
    url: a.href,
    protocol: a.protocol.slice(0, -1),
    domain: a.hostname,
    query: a.search.substring(1),
    path,
    hash: a.hash,
  }
}

export function getLocationUrl(): string | undefined {
  const location = getDefaultBrowser() && getDefaultLocation()
  return location?.href
}
