/* eslint-disable @typescript-eslint/no-invalid-void-type */
import { arrayRemove } from './object-array'
import { applyBufferTimer, BUFFER_TIME } from './time'

export type ParametersForFn<T extends (args: any) => any> = T extends (args: infer P) => any ? P : never

export type Observable<T extends (args: any) => any> = {
  subscribe: (f: T) => () => void
  unsubscribe: (f: T) => void
  next: (args: ParametersForFn<T>) => ReturnType<NextLink<T>>
  attach: (disconnect: () => void, onSubscribe?: (f: T) => void) => void
  complete: (f: () => void) => void
}

export type SafeObservable<T> = [(f: T) => () => void, (f: T) => void]

export type Observer<T extends (args: any) => any> = (
  next: (args: ParametersForFn<T>) => ReturnType<NextLink<T>>,
  attach: (disconnect: () => void, onSubscribe?: (f: T) => void) => void,
) => void

export type NextLink<T extends (args: any) => any> = (args: ParametersForFn<T>, cb: T[]) => NextLink<ReturnType<T>>

export const nextLink = <T extends (args: any) => any>(args: ParametersForFn<T>, cb: T[]) => {
  let nextCbs: ReturnType<T>[] = []
  try {
    nextCbs = cb.reduce((sum: ReturnType<T>[], s: T) => {
      try {
        const nextCb = s(args)
        typeof nextCb === 'function' && sum.push(nextCb)
      } catch {
        // do nothing
      }
      return sum
    }, [])
  } catch {
    // do nothing
  }
  return (nextArgs: ParametersForFn<ReturnType<T>>) => nextLink(nextArgs, nextCbs)
}

export const toObservable = <T extends (args: any) => any>(bufferTime: number = BUFFER_TIME): Observable<T> => {
  let subscribers: T[] = []
  const __complete: (() => void)[] = []
  let closed = false
  let attachCb: [() => void, ((f: T) => void) | undefined] | undefined

  // 暂缓的自动销毁机制，比如5min 没有人订阅就自动销毁
  const setCloseIfNeed = applyBufferTimer(
    () => !!subscribers.length,
    () => {
      closed = true
      attachCb && attachCb[0]()
      __complete.forEach((e) => e())
      __complete.length = 0
      attachCb = undefined
    },
    bufferTime,
  )

  const unsubscribe = (o: T) => {
    subscribers = arrayRemove(subscribers, o)
    !closed && setCloseIfNeed()
  }
  return {
    next: (args: ParametersForFn<T>) => {
      return nextLink(args, subscribers)
    },
    complete: (cb) => {
      __complete.push(cb)
    },
    attach: (disconnect: () => void, onSubscribe?: (f: T) => void) => {
      attachCb = [disconnect, onSubscribe]
    },
    subscribe: (o: T) => {
      if (closed) throw new Error('Observer is closed')
      subscribers.push(o)
      attachCb && attachCb[1] && attachCb[1](o)
      setCloseIfNeed()
      return () => unsubscribe(o)
    },
    unsubscribe,
  }
}

export const createSafeObserver = <T extends (args: any) => any>(
  observer: Observer<T>,
  completeCb?: () => void,
  bufferTime?: number,
): SafeObservable<T> => {
  const cur = toObservable<T>(bufferTime)
  try {
    observer(cur.next, cur.attach)
    completeCb && cur.complete(completeCb)
  } catch {
    //
  }
  return [cur.subscribe, cur.unsubscribe]
}
