/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import { initSubjectInGlobal, WithSubject, initPrivateSubject } from '@slardar/sdk-template'

import { applyPV, PV_SUBJECT_NAME } from '../../collector/client'
import { XHR_SUBJECT_NAME, applyXHR, fetchSubject } from '../../collector/http'
import { longtaskSubject, resourceSubject } from '../../collector/pref'
import { getTTI } from '../../monitors/performance-new/tti'
import { WebClient } from '../../types'

import { WithPerformancePrecollect } from './performance'
import { getPluginConfig, reportOnInitCommonParams } from './utils'

export const TTI_MONITOR_PLUGIN_NAME = 'tti'

export interface TTIMonitorPluginConfig {
  [TTI_MONITOR_PLUGIN_NAME]?: boolean | Record<string, unknown>
}

export function TTIMonitorPlugin<C extends WithPerformancePrecollect<WithSubject<WebClient>>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, TTI_MONITOR_PLUGIN_NAME, {})
    if (!config) {
      return
    }
    const tearDownGroup: (() => void)[] = []

    getTTI(
      reportOnInitCommonParams(client),
      tearDownGroup,
      [
        initSubjectInGlobal(client, [XHR_SUBJECT_NAME, applyXHR(XMLHttpRequest && XMLHttpRequest.prototype)]),
        initSubjectInGlobal(client, fetchSubject),
        initSubjectInGlobal(client, longtaskSubject),
        initSubjectInGlobal(client, resourceSubject),
        initPrivateSubject(client, [PV_SUBJECT_NAME, applyPV(client)]),
      ],
      client.pp,
    )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
  })
}
