import { SafeObservable } from '@slardar/sdk-template'

import { PVNext } from '../../collector/client'
import { XHRNext, FetchNext } from '../../collector/http'
import { LongtaskNext, ResourceNext } from '../../collector/pref'
import { WebReportEvent } from '../../types'
import { applyMutationObserver } from '../../utils'
import { now } from '../../utils/date'
import { applyIncompleteReq } from '../action'
import { waitActionComplete } from '../action/util'
import { initMetric } from '../performance/util'

import { SPA_LOAD_METRIC_NAME, applyReportMetric } from './util'

export type SPAGetterWithContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [
    SafeObservable<PVNext>,
    SafeObservable<XHRNext>,
    SafeObservable<FetchNext>,
    SafeObservable<ResourceNext>,
    SafeObservable<LongtaskNext>,
    typeof MutationObserver,
  ],
) => void

export const getSPA: SPAGetterWithContext = (
  report,
  tearDownGroup,
  [pvObserver, xhrObserver, fetchObserver, resourceObserver, longtaskObserver, MutationObserver],
) => {
  let spa_load_start = 0
  const reportMetric = applyReportMetric(report)

  const startSPA = () => {
    spa_load_start = now()
    observe(document, {
      childList: true,
      subtree: true,
    })
    start()
  }

  const reportSPAIfAllowed = () => {
    if (Object.keys(incompleteReq).length || !spa_load_start) {
      return
    }
    reportMetric(initMetric(SPA_LOAD_METRIC_NAME, now() - spa_load_start))
    endSPA()
  }

  const endSPA = () => {
    complete()
    spa_load_start = 0
    disconnect()
  }

  const [start, notify, complete] = waitActionComplete(reportSPAIfAllowed, endSPA, window)

  // request
  const incompleteReq = applyIncompleteReq(tearDownGroup, [xhrObserver, fetchObserver], notify)

  // resource
  tearDownGroup.push(resourceObserver[0](notify))
  // longtask
  tearDownGroup.push(longtaskObserver[0](notify))
  // dom
  const [observe, disconnect] = applyMutationObserver(MutationObserver, notify)

  tearDownGroup.push(disconnect)

  let isFirstPV = true
  // start SPA when PV change
  tearDownGroup.push(
    pvObserver[0](() => {
      if (isFirstPV) {
        isFirstPV = false
        return
      }
      // force end last SPA if not report
      endSPA()
      // start a new SPA metric
      startSPA()
    }),
  )
  // reset SPA status when tear down
  tearDownGroup.push(endSPA)
}
