import { SafeObservable } from '@slardar/sdk-template'

import { LoadNext, UnloadNext } from '../../collector/page'
import { LongtaskNext } from '../../collector/pref'
import { PerformanceTimingReport } from '../../types'
import { applyPerformance, getDefaultPerformance } from '../../utils'
import { MetricEnum } from '../performance/type'

import { PerfMetricGetterWithLoadContext } from './type'
import { applySendOnceAndTearDown } from './util'

export const TIMING_METRIC_NAME = MetricEnum.timing

const getTiming: PerfMetricGetterWithLoadContext = (
  report,
  tearDownGroup,
  [loadObserver, unloadObserver]: [SafeObservable<LoadNext>, SafeObservable<UnloadNext>, SafeObservable<LongtaskNext>],
) => {
  const performance = getDefaultPerformance()
  const [, , getEntriesByType] = applyPerformance(performance)
  const wrapTiming = (isBounced: boolean): PerformanceTimingReport => {
    const timing = (performance && performance.timing) || undefined
    const navigation = getEntriesByType('navigation')[0] as PerformanceNavigationTiming | undefined
    return {
      ev_type: 'performance_timing',
      payload: {
        isBounced,
        timing,
        navigation_timing: navigation,
      },
    }
  }

  const sendOnceAndTearDown = applySendOnceAndTearDown(wrapTiming, report, tearDownGroup)

  tearDownGroup.push(
    loadObserver[0](() => {
      sendOnceAndTearDown(false)
    }),
  )

  tearDownGroup.push(
    unloadObserver[0](() => {
      sendOnceAndTearDown(true)
    }),
  )
}

export const TIMING: [MetricEnum, PerfMetricGetterWithLoadContext] = [TIMING_METRIC_NAME, getTiming]
