const PAGE_ACTIVITY_DELAY = 100
const MAX_PAGE_ACTIVITY_DELAY = 10000
const ROOT_TAGS = ['BODY', 'HTML', 'HEAD']

/**
 * 控制等待策略和强制结算
 * 结算策略: 没有进行中的请求, 没有dom变更, 没有resource or longTask
 */
export const waitActionComplete = (cb: () => void, forceClear: () => void, win: Window) => {
  let idleTimer: number
  let maxIdleTimer: number
  let started = false

  const notify = () => {
    win.clearTimeout(idleTimer)
    started &&
      (idleTimer = win.setTimeout(() => {
        cb()
      }, PAGE_ACTIVITY_DELAY))
  }

  const start = () => {
    started = true
    win.clearTimeout(maxIdleTimer)
    maxIdleTimer = win.setTimeout(() => {
      cb()
      forceClear()
    }, MAX_PAGE_ACTIVITY_DELAY)
    notify()
  }

  const complete = () => {
    started = false
    win.clearTimeout(idleTimer)
    win.clearTimeout(maxIdleTimer)
  }
  return [start, notify, complete]
}

/**
 * 从target中获取path
 */
export const getDomPath = (element: HTMLElement): string | undefined => {
  let el = element
  const stack = []
  try {
    while (el.parentNode !== null) {
      let sibCount = 0
      let sibIndex = 0
      // eslint-disable-next-line @typescript-eslint/prefer-for-of
      for (let i = 0; i < el.parentNode.childNodes.length; i++) {
        const sib = el.parentNode.childNodes[i]
        if (sib.nodeName === el.nodeName) {
          if (sib === el) {
            sibIndex = sibCount
          }
          sibCount++
        }
      }
      if (el.hasAttribute('id') && el.id !== '') {
        stack.unshift(el.nodeName + '#' + el.id)
      } else if (sibCount > 1) {
        stack.unshift(el.nodeName + ':eq(' + sibIndex + ')')
      } else {
        stack.unshift(el.nodeName)
      }
      el = el.parentNode as HTMLElement
    }
    // removes the html element
    return stack.slice(1).join('>')
  } catch {
    return
  }
}

export const isSupportsElementClosest = (() => {
  let supportsElementClosest: boolean | undefined
  return () =>
    supportsElementClosest === undefined
      ? (supportsElementClosest = 'closest' in HTMLElement.prototype)
      : supportsElementClosest
})()

export const findElementWithAttributeByParents = (target: HTMLElement, attribute: string, maxParentLoops = 10) => {
  let element: Element | null = target
  for (let i = 0; i < maxParentLoops && element; i++) {
    if (~ROOT_TAGS.indexOf(element.nodeName)) return
    if (element.hasAttribute(attribute)) {
      return element
    }
    element = element.parentElement
  }
}

export const getActionNameByAttribute = (target: HTMLElement, attribute: string) => {
  let elementWithAttribute
  if (isSupportsElementClosest()) {
    elementWithAttribute = target.closest(`[${attribute}]`)
  } else {
    elementWithAttribute = findElementWithAttributeByParents(target, attribute)
  }
  if (!elementWithAttribute) return

  const name = elementWithAttribute.getAttribute(attribute)!
  // Possibly length of name is too long to be stored on backend.Let it go for now.Add truncation if someone oncalled
  return name.trim()
}

export const getActionName = (target: HTMLElement, attribute: string) => {
  return getActionNameByAttribute(target, attribute) || target.innerText
}
