import { hookObjectProperty, Observer } from '@slardar/sdk-template'

import { getDefaultBrowser, getDefaultHistory } from '../utils'

export const HISTORY_SUBJECT_NAME = 'history_0'

export const HASH_SUBJECT_NAME = 'hash_0'

export type RouteNext = (args: string) => void

export const observeHistory: Observer<RouteNext> = (next, tearDown) => {
  const history = getDefaultHistory()
  if (!history) return

  const restoreFns: (() => void)[] = []

  const historyChangeListener = () => next(location.href)
  const hookStateChange = (origin: any) =>
    function (...params: any) {
      try {
        origin.apply(history, params)
      } finally {
        historyChangeListener()
      }
    }
  restoreFns.push(
    hookObjectProperty(history, 'pushState', hookStateChange)(),
    hookObjectProperty(history, 'replaceState', hookStateChange)(),
  )

  window.addEventListener('popstate', historyChangeListener, true)
  restoreFns.push(() => window.removeEventListener('popstate', historyChangeListener, true))
  tearDown(() => {
    restoreFns.forEach((fn) => fn())
  })
}

export const observeHash: Observer<(args: string) => void> = (next, tearDown) => {
  const window = getDefaultBrowser()
  if (!window) return

  const hashChangeListener = () => next(location.href)
  window.addEventListener('hashchange', hashChangeListener, true)
  tearDown(() => window.removeEventListener('hashchange', hashChangeListener, true))
}

export const hashSubject: [string, Observer<RouteNext>] = [HASH_SUBJECT_NAME, observeHash]

export const historySubject: [string, Observer<RouteNext>] = [HISTORY_SUBJECT_NAME, observeHistory]
