/**
 * 生成uuid
 * stolen from https://github.com/kelektiv/node-uuid#readme uuid/v4
 *
 * @returns
 */

function mathRNG() {
  const rnds: number[] = new Array(16)

  let r = 0

  for (let i = 0; i < 16; i++) {
    if ((i & 0x03) === 0) {
      r = Math.random() * 0x100000000
    }
    rnds[i] = (r >>> ((i & 0x03) << 3)) & 0xff
  }

  return rnds
}

function bytesToUuid(buf: number[]) {
  const byteToHex = [] as string[]
  for (let index = 0; index < 256; ++index) {
    byteToHex[index] = (index + 0x100).toString(16).substr(1)
  }

  let i = 0
  const bth = byteToHex
  // join used to fix memory issue caused by concatenation: https://bugs.chromium.org/p/v8/issues/detail?id=3175#c4
  return [
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
    '-',
    bth[buf[i++]],
    bth[buf[i++]],
    '-',
    bth[buf[i++]],
    bth[buf[i++]],
    '-',
    bth[buf[i++]],
    bth[buf[i++]],
    '-',
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
    bth[buf[i++]],
  ].join('')
}

function uuid() {
  const rnds: number[] = mathRNG()

  // Per 4.4, set bits for version and `clock_seq_hi_and_reserved`
  rnds[6] = (rnds[6] & 0x0f) | 0x40
  rnds[8] = (rnds[8] & 0x3f) | 0x80

  return bytesToUuid(rnds)
}

export { uuid }
