import { id, initSubjectInGlobal, WithSubject, createSafeObserver, WithContext } from '@slardar/sdk-template'

import { fetchSubject, hookFetch } from '../../collector/http'
import { resourceSubject } from '../../collector/pref'
import { httpGetterWithFetchObserver } from '../../monitors/http/fetch'
import { setTraceContext, setVolTraceContext } from '../../monitors/http/trace'
import { FetchMonitorConfig } from '../../monitors/http/type'
import { WebClient, WebVolClient } from '../../types'

import { getCbHook } from './ajax'
import { getPluginConfig } from './utils'

export const FETCH_MONITOR_PLUGIN_NAME = 'fetch'

export interface WrapFetch {
  (fetch: typeof window.fetch): typeof window.fetch
}

export type WithWrapFetch<C extends WebClient> = C & {
  wrapFetch?: WrapFetch
}

const defaultConfig: FetchMonitorConfig = {
  autoWrap: true,
  hookCbAtReq: id,
  ignoreUrls: [],
  collectBodyOnError: false,
}
export interface FetchMonitorPluginConfig {
  [FETCH_MONITOR_PLUGIN_NAME]?: Partial<FetchMonitorConfig> | boolean
}

export function FetchMonitorPlugin<C extends WithContext<WithSubject<WebClient>>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, FETCH_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    const fetchConfig = { ...config, hookCbAtReq: getCbHook(client), setTraceHeader: setTraceContext(config.trace) }
    const getResourceObserver = () => initSubjectInGlobal(client, resourceSubject)

    fetchConfig.autoWrap &&
      httpGetterWithFetchObserver(
        client.report.bind(client),
        tearDownGroup,
        [initSubjectInGlobal(client, fetchSubject), getResourceObserver],
        fetchConfig,
      )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })

    client.provide('wrapFetch', (f: typeof window.fetch) => {
      let newFetch = undefined

      httpGetterWithFetchObserver(
        client.report.bind(client),
        tearDownGroup,
        [
          createSafeObserver((next) => {
            newFetch = hookFetch(f, next)
          }),
          getResourceObserver,
        ],
        fetchConfig,
      )

      return newFetch
    })
  })
}

export function FetchMonitorVolPlugin<C extends WithSubject<WebVolClient>>(client: C) {
  client.on('init', () => {
    const config = getPluginConfig(client, FETCH_MONITOR_PLUGIN_NAME, defaultConfig)
    if (!config) {
      return
    }

    const tearDownGroup: (() => void)[] = []

    const fetchConfig = {
      ...config,
      hookCbAtReq: getCbHook(client),
      setTraceHeader: setVolTraceContext(config.trace, `app_id=${client.config()?.aid},origin=web`),
    }

    fetchConfig.autoWrap &&
      httpGetterWithFetchObserver(
        client.report.bind(client),
        tearDownGroup,
        [initSubjectInGlobal(client, fetchSubject), () => initSubjectInGlobal(client, resourceSubject)],
        fetchConfig,
      )

    client.on('beforeDestroy', () => {
      tearDownGroup.forEach((e) => e())
    })
  })
}
