import { SafeObservable, getRegexp, arrayIncludes } from '@slardar/sdk-template'

import { LoadNext } from '../../collector/page'
import { ResourceNext } from '../../collector/pref'
import { WebReportEvent } from '../../types'
import { getDefaultPerformance, applyPerformance } from '../../utils'

export interface ResourceProps {
  ignoreUrls: (string | RegExp)[]
  slowSessionThreshold: number
  ignoreTypes?: string[]
}

type ResourceGetterWithContext = (
  report: (ev: WebReportEvent) => void,
  tearDownGroup: (() => void)[],
  context: [SafeObservable<LoadNext>, () => SafeObservable<ResourceNext>],
  config: ResourceProps,
) => void

const RESOURCE_EV_TYPE = 'resource'
const RESOURCE_PERFORMANCE_ENTRY_TYPE = 'resource'
const RESOURCE_IGNORE_TYPES = ['xmlhttprequest', 'fetch', 'beacon']

export const resourceGetterWithContext: ResourceGetterWithContext = (
  report,
  tearDownGroup,
  [loadObserver, getResourceObserver],
  config,
) => {
  const performance = getDefaultPerformance()
  if (!performance) return
  const { ignoreUrls, slowSessionThreshold, ignoreTypes } = config
  const ignoreRegExp = getRegexp(ignoreUrls)

  const reportResource = (entry: PerformanceResourceTiming, isSlowSession = false) => {
    if (
      arrayIncludes(ignoreTypes || RESOURCE_IGNORE_TYPES, entry.initiatorType) ||
      (ignoreRegExp && ignoreRegExp.test(entry.name))
    )
      return

    const data: WebReportEvent = { ev_type: RESOURCE_EV_TYPE, payload: entry }
    isSlowSession && (data.extra = { sample_rate: 1 })
    report(data)
  }

  tearDownGroup.push(
    loadObserver[0](() => {
      const [timing, , getEntriesByType] = applyPerformance(performance)

      const checkSlowSession = () => {
        if (!timing) {
          return false
        }
        const loadTime = timing.loadEventEnd - timing.navigationStart
        return loadTime > slowSessionThreshold
      }
      const isSlowSession = checkSlowSession()
      const cacheEntries = getEntriesByType(RESOURCE_PERFORMANCE_ENTRY_TYPE) as PerformanceResourceTiming[]
      cacheEntries.forEach((e) => reportResource(e, isSlowSession))

      tearDownGroup.push(
        getResourceObserver()[0]((entry) => {
          reportResource(entry)
        }),
      )
    }),
  )
}
