/* eslint-disable @typescript-eslint/prefer-for-of */
/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
import {
  arrayIncludes,
  isHitBySampleRate,
  isHitByRandom,
  isBoolean,
  isNumber,
  safeVisit,
  log,
  id,
} from '@slardar/sdk-template'

import { WebReport, WebClient, SampleConfig, EventSampleRule, ConditionalSampleRule, Filter } from '../types'
import { reportSelfError } from '../utils'

interface HitBySampleRateFn {
  (sampleRate: number): boolean
}
interface HitByRandomFn {
  (random: number, sampleRate: number): boolean
}

type EventHitRule = Omit<EventSampleRule, 'name' | 'conditional_sample_rules'> & {
  hit: () => boolean
  effectiveSampleRate: number
  conditional_hit_rules?: ConditionalHitRule[]
}

type ConditionalHitRule = ConditionalSampleRule & {
  hit: () => boolean
  effectiveSampleRate: number
}

type SendEvent = WebReport & { common?: any }

const withSampleRate = (ev: SendEvent, sampleRate: number) => {
  const common = ev.common || {}
  common.sample_rate = sampleRate
  ev.common = common
  return ev
}

const hitFnWithRandom = (
  preCalc: boolean,
  sampleRate: number,
  isHitBySampleRate: HitBySampleRateFn,
  random: number,
  isHitByRandom: HitByRandomFn,
) => {
  return preCalc
    ? (
        (h) => () =>
          h
      )(isHitByRandom(random, sampleRate))
    : () => isHitBySampleRate(sampleRate)
}

export const parseValues = (values: string[], type: string) => {
  return values.map((v) => {
    switch (type) {
      case 'number':
        return Number(v)
      case 'boolean':
        return v === '1'
      case 'string': // default to string
      default:
        return String(v)
    }
  })
}

export const checkVal = (val: any, values: any[], op: string): boolean => {
  switch (op) {
    case 'eq':
      return arrayIncludes(values, val)
    case 'neq':
      return !arrayIncludes(values, val)
    case 'gt':
      return val > values[0]
    case 'gte':
      return val >= values[0]
    case 'lt':
      return val < values[0]
    case 'lte':
      return val <= values[0]
    case 'regex':
      return Boolean(val.match(new RegExp(values.join('|') as string)))
    case 'not_regex':
      return !val.match(new RegExp(values.join('|') as string))
    default: {
      // unknown op
      return false
    }
  }
}

export const checkFilter = (ev: SendEvent, field: string, op: string, values: string[]): boolean => {
  const val = safeVisit(ev, field, (t, p) => {
    return t[p]
  })
  if (val === undefined) {
    return false
  }
  const field_type = isBoolean(val) ? 'bool' : isNumber(val) ? 'number' : 'string'
  return checkVal(val, parseValues(values, field_type), op)
}

export const matchFilter = (ev: SendEvent, filter: Filter): boolean => {
  try {
    return filter.type === 'rule'
      ? checkFilter(ev, filter.field!, filter.op!, filter.values!)
      : filter.type === 'and'
      ? filter.children!.every((f) => matchFilter(ev, f))
      : filter.children!.some((f) => matchFilter(ev, f))
  } catch (e) {
    reportSelfError(e)
    return false
  }
}

export const getHitMap = (
  rules: SampleConfig['rules'],
  preCalcHit: boolean,
  baseRate: number,
  isHitBySampleRate: HitBySampleRateFn,
  random: number,
  isHitByRandom: HitByRandomFn,
) => {
  const hitMap = {} as Record<string, EventHitRule>
  Object.keys(rules).forEach((name) => {
    const { enable, sample_rate, conditional_sample_rules } = rules[name]
    if (enable) {
      hitMap[name] = {
        enable,
        sample_rate,
        effectiveSampleRate: sample_rate * baseRate,
        hit: hitFnWithRandom(preCalcHit, sample_rate, isHitBySampleRate, random, isHitByRandom),
      }
      if (conditional_sample_rules) {
        hitMap[name].conditional_hit_rules = conditional_sample_rules.map(
          ({ sample_rate: s, filter }) =>
            ({
              sample_rate: s,
              hit: hitFnWithRandom(preCalcHit, s, isHitBySampleRate, random, isHitByRandom),
              effectiveSampleRate: s * baseRate,
              filter,
            } as ConditionalHitRule),
        )
      }
    } else {
      hitMap[name] = {
        enable,
        hit: () => {
          /* istanbul ignore next */
          return false
        },
        sample_rate: 0,
        effectiveSampleRate: 0,
      }
    }
  })
  return hitMap
}

export const getSampler = (
  userId: string,
  config: SampleConfig | undefined,
  isHitBySampleRate: HitBySampleRateFn,
  isHitByRandom: HitByRandomFn,
): ((ev: SendEvent) => SendEvent | false) => {
  if (!config) return id
  // r的设计是为了允许外部传入随机数，用于彻底实现按用户采样
  const { sample_rate: baseRate, include_users, sample_granularity, rules, r: random = Math.random() } = config

  // 用户名单采样
  const userHit = arrayIncludes(include_users, userId)
  if (userHit) {
    return (ev: SendEvent) => withSampleRate(ev, 1)
  }

  // should pre calculate hit
  const preCalcHit = sample_granularity === 'session'
  const baseHit = hitFnWithRandom(preCalcHit, baseRate, isHitBySampleRate, random, isHitByRandom)

  const hitMap = getHitMap(rules, preCalcHit, baseRate, isHitBySampleRate, random, isHitByRandom)

  return (ev: SendEvent) => {
    log('[sample]', ev)
    // 总采样必须命中才有后续
    if (!baseHit()) {
      log('[sample] base miss')
      return false
    }

    // 未配置的事件类型
    if (!(ev.ev_type in hitMap)) {
      log('[sample] base hit')
      return withSampleRate(ev, baseRate)
    }

    // 忽略未开启的事件类型
    if (!hitMap[ev.ev_type].enable) {
      log('[sample] ev_type disabled', ev.ev_type)
      return false
    }

    // 跳过采样配置
    if (ev.common?.sample_rate) {
      log('[sample] skip')
      return ev
    }

    const hitConfig = hitMap[ev.ev_type]
    const conditions = hitConfig.conditional_hit_rules
    if (conditions) {
      // 先判断条件采样
      for (let i = 0; i < conditions.length; i++) {
        if (matchFilter(ev, conditions[i].filter)) {
          log('[sample] filter matched')
          if (conditions[i].hit()) {
            log('[sample] filter hit')
            return withSampleRate(ev, conditions[i].effectiveSampleRate)
          }
          log('[sample] filter miss')
          // 条件匹配后不再搜索
          return false
        }
      }
    }

    // 事件类型采样
    if (!hitConfig.hit()) {
      log('[sample] ev_type miss')
      return false
    }

    log('[sample] ev_type hit')
    // 事件类型默认采样已经命中
    return withSampleRate(ev, hitConfig.effectiveSampleRate)
  }
}

export const SamplePlugin = <C extends WebClient>(client: C) => {
  client.on('start', () => {
    const { userId, sample } = client.config()!
    if (sample && sample.sample_rate === 0) {
      client.destroy()
    }
    const sampler = getSampler(userId, sample, isHitBySampleRate, isHitByRandom)
    client.on('build', sampler)
  })
}
