/* eslint-disable @typescript-eslint/prefer-nullish-coalescing */
/* eslint-disable @typescript-eslint/prefer-optional-chain */
/* eslint-disable @typescript-eslint/ban-types */
import { isFunction, hookObjectProperty } from '@slardar/sdk-template'

import { JsErrorPayload } from '../../types'
import { getDefaultBrowser, getDefaultXMLHttpRequest } from '../../utils'

import { normalize } from './util'

export const DEFAULT_EVENT_TARGET = [
  'EventTarget',
  'Window',
  'Node',
  'ApplicationCache',
  'ChannelMergerNode',
  'EventSource',
  'FileReader',
  'HTMLUnknownElement',
  'IDBDatabase',
  'IDBRequest',
  'IDBTransaction',
  'MessagePort',
  'Notification',
  'SVGElementInstance',
  'Screen',
  'TextTrack',
  'TextTrackCue',
  'TextTrackList',
  'WebSocket',
  'Worker',
  'XMLHttpRequest',
  'XMLHttpRequestEventTarget',
  'XMLHttpRequestUpload',
] as const

export const DEFAULT_TIME_FUNCTION = [
  'setTimeout',
  'setInterval',
  'requestAnimationFrame',
  'requestIdleCallback',
] as const
export const xmlHttpRequestProps = ['onload', 'onerror', 'onprogress', 'onreadystatechange'] as const
const ADD_EVENT_LISTENER = 'addEventListener'
const REMOVE_EVENT_LISTENER = 'removeEventListener'
interface WrappedFunction extends Function {
  _w_?: Function
  handleEvent?: Function
}

interface HookFunction extends Function {
  _hook_?: boolean
}

interface CaptureJsErrorSourceData {
  function: string
  target?: string
}
const DEFAULT_SOURCE_TYPE = 'capture-global'

export const hookGlobalAsync = (
  report: ({ error, extra, react }: Partial<JsErrorPayload>) => void,
  window = getDefaultBrowser()!,
  xhr = getDefaultXMLHttpRequest()!,
) => {
  const restoreFns: (() => void)[] = []
  const wrap = (fn: WrappedFunction, sourceData: Partial<CaptureJsErrorSourceData>) => {
    if (!isFunction(fn)) return fn
    const source = {
      type: DEFAULT_SOURCE_TYPE,
      data: {
        ...sourceData,
      } as Record<string, string>,
    }

    const hookFn = (fn._w_ ||= function (this: any) {
      try {
        // NOTE: If you are a Slardar user, and you are seeing this stack frame, it
        //       means the Slardar SDK caught an error invoking your application code. This
        //       is expected behavior and NOT indicative of a bug with Slardar SDK.
        return (fn.handleEvent && isFunction(fn.handleEvent) ? fn.handleEvent : fn).apply(
          this,
          [].map.call(arguments, (arg) => wrap(arg, sourceData)),
        )
      } catch (e) {
        const error = normalize(e)
        error && report({ error, source })
        throw e
      }
    }) as HookFunction

    hookFn._hook_ = true

    return hookFn
  }

  window &&
    restoreFns.push(
      ...DEFAULT_TIME_FUNCTION.filter((timeFunc) => window[timeFunc]).map((timeFunc) =>
        hookObjectProperty(
          window,
          timeFunc,
          (origin: Function) =>
            function (this: any, handler: Function, ...params: any[]) {
              return (
                origin &&
                origin.call(
                  this,
                  wrap(handler, {
                    function: timeFunc,
                  }),
                  ...params,
                )
              )
            },
          false,
        )(),
      ),
    )

  xhr &&
    xhr.prototype &&
    restoreFns.push(
      hookObjectProperty(
        xhr.prototype,
        'send',
        (origin) =>
          function (this: XMLHttpRequest, ...params) {
            // filter 防御一个 xhr 实例被多次 send 导致 wrap 多次的情况
            xmlHttpRequestProps
              .filter((prop) => this[prop] && !(this[prop] as HookFunction)._hook_)
              .forEach((prop) => {
                // never restore xhr instance callback
                this[prop] = wrap(this[prop] as WrappedFunction, { function: prop }) as any
              })
            return origin.apply(this, params)
          },
        false,
      )(),
    )

  DEFAULT_EVENT_TARGET.forEach((target) => {
    const proto = window[target] && window[target].prototype
    if (!proto || !proto[ADD_EVENT_LISTENER]) {
      return
    }

    restoreFns.push(
      hookObjectProperty(
        proto,
        ADD_EVENT_LISTENER,
        (origin: WrappedFunction) =>
          function (this: any, evName: string, fn: WrappedFunction, options: {}) {
            try {
              const handleEventFn = fn.handleEvent
              if (isFunction(handleEventFn)) {
                fn.handleEvent = wrap(handleEventFn, { function: 'handleEvent', target })
              }
            } catch {
              //
            }
            return origin && origin.call(this, evName, wrap(fn, { function: ADD_EVENT_LISTENER, target }), options)
          },
        false,
      )(),
    )

    restoreFns.push(
      hookObjectProperty(
        proto,
        REMOVE_EVENT_LISTENER,
        (origin: WrappedFunction) =>
          function (this: any, eventName: string, fn: WrappedFunction, options: {}) {
            if (fn?._w_) {
              origin.call(this, eventName, fn._w_, options)
            }
            return origin.call(this, eventName, fn, options)
          },
        false,
      )(),
    )
  })

  return restoreFns
}
