import { isFunction, isObject } from '@slardar/sdk-template'

export function getDefaultBrowser(): undefined | Window {
  if (typeof window === 'object' && isObject(window)) return window
}

export function getDefaultDocument(): undefined | Document {
  if (typeof document === 'object' && isObject(document)) return document
}

export function getDefaultLocation(): undefined | Location {
  return getDefaultBrowser() && window.location
}

export function getDefaultHistory(): undefined | History {
  // eslint-disable-next-line compat/compat
  return getDefaultBrowser() && window.history
}

export function getDefaultPerformance(): undefined | Performance {
  if (getDefaultBrowser() && isObject(window.performance)) return window.performance
}

export function getDefaultPromise(): undefined | typeof Promise {
  if (getDefaultBrowser() && 'Promise' in window) return Promise
}

export function getDefaultXMLHttpRequest(): undefined | typeof XMLHttpRequest {
  if (typeof XMLHttpRequest === 'function' && isFunction(XMLHttpRequest)) return XMLHttpRequest
}

export function getDefaultFetch(): undefined | typeof window.fetch {
  try {
    // eslint-disable-next-line compat/compat
    new Headers()
    // eslint-disable-next-line compat/compat
    new Request('')
    // eslint-disable-next-line compat/compat
    new Response()
    // eslint-disable-next-line compat/compat
    return window.fetch
  } catch {
    //
  }
}

export function getDefaultMutationObserver(): undefined | typeof MutationObserver {
  if (getDefaultBrowser() && isFunction(window.MutationObserver)) return window.MutationObserver
}

export function getDefaultPerformanceObserver(): undefined | typeof PerformanceObserver {
  if (getDefaultBrowser() && isFunction(window.PerformanceObserver)) return window.PerformanceObserver
}

export function getDefaultPerformanceTiming(): undefined | PerformanceTiming {
  const performance = getDefaultPerformance()
  if (performance && isObject(performance.timing)) return performance.timing
}

export function getDefaultRaf(): undefined | Window['requestAnimationFrame'] {
  if (getDefaultBrowser() && 'requestAnimationFrame' in window) {
    return window.requestAnimationFrame
  }
}

export function getDefaultCaf(): undefined | Window['cancelAnimationFrame'] {
  if (getDefaultBrowser() && 'cancelAnimationFrame' in window) {
    return window.cancelAnimationFrame
  }
}

export function getDefaultNavigator(): undefined | Window['navigator'] {
  if (getDefaultBrowser() && 'navigator' in window) {
    return window.navigator
  }
}

export interface NetworkInformation {
  readonly effectiveType?: string
  readonly type?: string
  readonly rtt?: number
  onchange?: () => void
}
export function getDefaultNetworkInformation(): undefined | NetworkInformation {
  const navigator = getDefaultNavigator()
  if (navigator) {
    return (navigator as any).connection || (navigator as any).mozConnection || (navigator as any).webkitConnection
  }
}

export function getCurrentScript(): HTMLOrSVGScriptElement | null {
  if (!document) return null
  if (document.currentScript) return document.currentScript

  // IE 8-10 support script readyState
  // IE 11+ support stack trace
  try {
    throw new Error()
  } catch (err: any) {
    // Find the second match for the "at" string to get file src url from stack.
    // Specifically works with the format of stack traces in IE.
    let i = 0
    const stackDetails = /at\s+(.*)\s+\((.*):(\d*):(\d*)\)/i.exec(err.stack)
    // eslint-disable-next-line @typescript-eslint/prefer-optional-chain
    const scriptLocation = (stackDetails && stackDetails[2]) || false
    // eslint-disable-next-line @typescript-eslint/prefer-optional-chain
    const line = (stackDetails && stackDetails[3]) || 0
    const currentLocation = document.location.href.replace(document.location.hash, '')
    let inlineScriptSource = ''
    const scripts = document.getElementsByTagName('script') // Live NodeList collection

    if (scriptLocation === currentLocation) {
      const pageSource = document.documentElement.outerHTML
      const inlineScriptSourceRegExp = new RegExp(
        '(?:[^\\n]+?\\n){0,' + ((line as number) - 2) + '}[^<]*<script>([\\d\\D]*?)<\\/script>[\\d\\D]*',
        'i',
      )
      inlineScriptSource = pageSource.replace(inlineScriptSourceRegExp, '$1').trim()
    }

    for (; i < scripts.length; i++) {
      // If ready state is interactive, return the script tag
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      if (scripts[i].readyState === 'interactive') {
        return scripts[i]
      }

      // If src matches, return the script tag
      if (scripts[i].src === scriptLocation) {
        return scripts[i]
      }

      // If inline source matches, return the script tag
      if (
        scriptLocation === currentLocation &&
        scripts[i].innerHTML &&
        scripts[i].innerHTML.trim() === inlineScriptSource
      ) {
        return scripts[i]
      }
    }

    // If no match, return null
    return null
  }
}
